<template>
    <div class="container">
        <van-tabs 
            class="pay-type"
            v-model="activeName"
            title-inactive-color="#818594" 
            title-active-color="#292C34">
            <van-tab class="tab1" title="未付费" name="noPay">
                <template #title>
                    <span class="custom-tab-title">未付费</span>
                </template>
                <van-collapse v-model="activeNames" v-for="(item, index) in notPayListData" :key="'notpay' + index">
                    <van-collapse-item class="colp-item" :name="item.visitNo" title-class="tit-style" value-class="val-style" :title="item.visitNoLabel" :value="item.deptName">
                        <template #default>
                            <van-checkbox-group v-model="result[index]" ref="checkboxGroup">
                                <van-row class="chk-item" type="flex" align="center" v-for="(item, cIndex) in item.summarys" :key="'chkItem' + cIndex">
                                    <van-col class="chk-text" span="19">
                                        <van-checkbox
                                        :ref="'mark' + index" 
                                        :data-itemtypeno="item.itemTypeNo" 
                                        :data-prescriptionno="item.prescriptionNo" 
                                        :data-consume="Number(item.consume).toFixed(2)" 
                                        :name="item.prescriptionNo" 
                                        @click="handleChkClick(index)"
                                        >{{item.itemTypeName}} {{Number(item.consume).toFixed(2)}}</van-checkbox>
                                    </van-col>
                                    <van-col class="chk-text" span="5" @click="handleGoDetailModal(item.details)">明细 ></van-col>
                                </van-row>
                            </van-checkbox-group>
                            <div class="chk-item chk-all">
                                <van-checkbox v-model="checkAll[index]" @click="handleCheckAll(index)">全选</van-checkbox>
                                <span>已选费用：{{totalConsume[index]}}元</span>
                            </div>
                            <div class="van-hairline--top pay-btn"><van-button type="info" size="small" :disabled="btnDisabled[index]" @click="handleCharge(index)">立即支付</van-button></div>
                        </template>
                    </van-collapse-item>
                </van-collapse>
                <van-empty description="暂无数据" v-if="notPayListData.length == 0"/>
            </van-tab>
            
            <van-tab class="tab2" title="已付费（当天）" name="paied">
                <div class="panel" v-for="(item, index) in paiedListData" :key="'paied' + index">
                    <div class="row">
                        <span class="col1">门诊号：{{item.visitNo}}</span>
                        <span class="col2">{{item.deptName}}</span>
                    </div>
                    <div class="row">
                        <span class="col1">费用总额：{{Number(item.consume).toFixed(2)}}元</span>
                    </div>
                    <van-cell class="detail" :title="Number(item.consume).toFixed(2) + '元'" :icon="require('../../../assets/images/img/jf-default.png')">
                        <template>
                            <span class="detail-btn" @click="handleGoDetailModal(item.Details)">明细 ></span>
                        </template>
                    </van-cell>
                </div>
                <van-empty description="暂无数据" v-if="paiedListData.length == 0"/>
            </van-tab>
        </van-tabs>
        <van-popup class="radius-10" v-model="showDetailModal">
            <div class="detail-modal">
                <van-cell class="detail-modal-title" title="明细" title-style="text-align: left">
                    <template #right-icon>
                        <van-icon @click="handleGoDetailModal" name="cross" style="line-height: inherit;" />
                    </template>
                </van-cell>
                <van-row class="table-title">
                    <van-col span="6">名称</van-col>
                    <van-col span="6">金额（元）</van-col>
                    <van-col span="6">数量</van-col>
                    <van-col span="6">规格</van-col>
                </van-row>
                <van-row class="table-content" type="flex" align="center" v-for="(item, index) in detailList" :key="'detail' + index">
                    <van-col span="6">{{item.itemName}}</van-col>
                    <van-col span="6">{{item.consume && Number(item.consume).toFixed(2)}}</van-col>
                    <van-col span="6">{{item.number && Number(item.number).toFixed(2)}}</van-col>
                    <van-col span="6">{{item.spec}}</van-col>
                </van-row>
            </div>
            
        </van-popup>
    </div>
</template>
<script>
export default {
    name: "Pay",
    data() {
        return {
            activeName: "noPay",
            activeNames: [],
            result: [],
            checkAll: [],
            showDetailModal: false,
            notPayListData: "",
            paiedListData: "",
            detailList: "",
            totalConsume: [],
            btnDisabled: [],
            attach: []
        };
    },
    mounted() {
        this.patId = this.$route.query.patId;
        let owner = this;
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        owner.globalMethods.setRequestHeader();
        this.$axios({
                method:'post',
                url: owner.$store.getters.queryNotPayList,
                data: {
                    patId: owner.patId
                }
            }).then ((response) => {
                if (response.data.Code == 0) {
                    owner.notPayListData = owner.wrapNotPayListData(response.data.Data);
                    owner.$toast.clear();
                }
                else if (response.data.Code == -2) {
                    owner.$toast({
                        type: "fail",
                        message: "toke过期，即将重新登录",
                        forbidClick: true,
                        onOpened: () => {
                            owner.globalMethods.delCookie("token");
                            owner.$router.push("Outpatient");
                        }
                    });
                }
                else {
                    owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: true
                        })
                }
            });
            owner.globalMethods.setRequestHeader();
            this.$axios({
                method:'post',
                url: owner.$store.getters.queryPaiedList,
                data: {
                    patId: owner.patId
                }
            }).then ((response) => {
                if (response.data.Code == 0) {
                    owner.paiedListData = response.data.Data;
                }
                else if (response.data.Code == -2) {
                    owner.$toast({
                        type: "fail",
                        message: "toke过期，即将重新登录",
                        forbidClick: true,
                        onOpened: () => {
                            owner.globalMethods.delCookie("token");
                            owner.$router.push("Outpatient");
                        }
                    });
                }
            })
    },
    methods: {
        handleGoDetailModal(data) {
            this.detailList = data;
            this.showDetailModal = !this.showDetailModal;
        },
        handleChkClick(index) {
            let summarys =  this.notPayListData[index].summarys;
            let Arr = summarys.map(function(item) {
                return item.prescriptionNo
            });
            let setArr = new Set(Arr);
            let newVal = this.result[index].length == setArr.size;
            this.$set(this.checkAll, index, newVal);
            this.handleChkChange(index);
        },
        handleChkChange(index) {
            let owner = this;
            setTimeout(function() {
                let chkDom = owner.$refs["mark" + index];
                let count = 0;
                owner.attach[index] = "";
                owner.$set(owner.btnDisabled, index, true);
                let itemAttach = "";
                for (let i = 0; i < chkDom.length; i++) {
                    let curItem = chkDom[i].$el;
                    let curItemChildClass = curItem.children[0].className;
                    // 若用curItem.ariaChecked来判断，则安卓系统的微信浏览器获取不到值，ios系统的微信正常
                    if (curItemChildClass.includes("van-checkbox__icon--checked")) {
                        owner.$set(owner.btnDisabled, index, false);
                        let dataset = curItem.dataset;
                        count += Number(dataset.consume);
                        itemAttach += dataset.prescriptionno + "," + dataset.itemtypeno + "|";
                    }
                }
                owner.$set(owner.attach, index, itemAttach);
                owner.$set(owner.totalConsume, index, count.toFixed(2));
            }, 200)
        },
        handleCheckAll(index) {
            this.$refs.checkboxGroup[index].toggleAll(this.checkAll[index]);
            let curResult = Array.from(new Set(this.result[index]));
            this.$set(this.result, index, curResult);
            this.handleChkChange(index);
        },
        handleCharge(index) {
            this.$set(this.btnDisabled, index, true);
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                method:'post',
                url: owner.$store.getters.getJsApi,
                data: {
                    patId: owner.patId,
                    body: "个人缴费",
                    fee: owner.totalConsume[index],
                    detail: "个人缴费",
                    visitNo: owner.notPayListData[index].visitNo,
                    attach: "3-" + owner.notPayListData[index].visitNo + "-" + owner.attach[index].slice(0, owner.attach[index].length - 1)
                }
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    let systemType = owner.globalMethods.getCookie("systemType");
                    if (systemType == "0A") {
                        owner.alipayCharge(resData);
                    }
                    else if (systemType == "0B") {
                        owner.wxCharge(resData);
                    }
                }
                else if (response.data.Code == -2) {
                    owner.$toast({
                        type: "fail",
                        message: "toke过期，即将重新登录",
                        forbidClick: true,
                        onOpened: () => {
                            owner.globalMethods.delCookie("token");
                            owner.$router.push("Outpatient");
                        }
                    });
                }
                else {
                    owner.$set(owner.btnDisabled, index, false);
                    owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: true
                        })
                }
            });
        },
        wrapNotPayListData(data) {
            for (let i = 0; i < data.length; i++) {
                this.activeNames.push(data[0].visitNo);
                this.result.push([]);
                this.checkAll.push(false);
                this.btnDisabled.push(true);
                this.attach.push("");
                this.totalConsume.push("0.00");
                let curItem = data[i];
                curItem.visitNoLabel = "门诊号: " + curItem.visitNo;
            }
            
            return data;
        },
        wxCharge(resData) {
            function onBridgeReady() {
                WeixinJSBridge.invoke(
                    'getBrandWCPayRequest',
                    {
                        "appId": resData.appId,//公众号名称，由商户传入     
                        "timeStamp": resData.timeStamp,//时间戳，自1970年以来的秒数
                        "nonceStr": resData.nonceStr, //随机串     
                        "package": resData.package,     
                        "signType": resData.signType,//微信签名方式：     
                        "paySign": resData.paySign //微信签名 
                    },
                    function(res) {
                        if (res.err_msg == "get_brand_wcpay_request:ok") {
                            //关闭微信浏览器  安卓系统
                            document.addEventListener('WeixinJSBridgeReady', function(){ WeixinJSBridge.call('closeWindow'); }, false);
                            //关闭微信浏览器   ios系统
                            WeixinJSBridge.call('closeWindow');
                        }
                        else {
                            owner.$set(owner.btnDisabled, index, false);
                        }
                    }
                );
            };
            // 判断是否是微信环境
            if (typeof WeixinJSBridge == "undefined") {
                owner.$set(owner.btnDisabled, index, false);
                if (document.addEventListener) {
                    document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                }
                else if (document.attachEvent) {
                    document.attachEvent('WeixinJSBridgeReady', onBridgeReady); 
                    document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                }
            }
            else {
                onBridgeReady();
            }
        },
        alipayCharge(resData) {
            function ready(callback) {
                // 如果jsbridge已经注入则直接调用
                if (window.AlipayJSBridge) {
                    callback && callback();
                } else {
                    // 如果没有注入则监听注入的事件
                    document.addEventListener('AlipayJSBridgeReady', callback, false);
                }
            };
            ready(function(){
                AlipayJSBridge.call("tradePay", {
                    orderStr: resData.strPara
                }, function(result) {
                    AlipayJSBridge.call('closeWebview');
                });
            });
        }
    }
};

</script>
<style scoped>
.container {
    min-height: 100vh;
    background-color: #F3F5F9;
    padding: 10px 15px;
}
.tab1 .colp-item {
    margin-top: 20px;
    text-align: left;
}
.tab1 .chk-text:nth-child(2n) {
   text-align: right;
   color: #0078FF;
}
.tab1 >>> .tit-style {
    text-align: left;
    color: #0078FF;
}
.tab1 >>> .val-style {
    color: #2c3e50;
}
.tab1 .chk-item {
    padding: 8px 0;
}
.tab1 .chk-item >>> .van-checkbox__label {
    width: 100%;
}
.tab1 .chk-all {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #2c3e50;
}
.tab1 .pay-btn {
    padding-top: 10px;
    text-align: center;
    border-top: 0.5px solid #d4d8dd;
}
.tab2 .panel {
    background-color: #fff;
    margin: 20px 0;
    padding: 10px 15px 0 15px;
    text-align: left;
    border-radius: 10px;
    overflow: hidden;
}
.tab2 .row {
    margin-bottom: 10px;
    font-size: 14px;
}
.tab2 .row .col1 {
    color: #0078FF;
}
.tab2 .row .col2  {
    float: right;
}
.tab2 .detail {
    padding: 10px 0;
}
.tab2 .detail-btn {
    color: #0078FF;
    padding: 5px;
}
.detail-modal {
    width: 300px;
    min-height: 300px;
    font-size: 12px;
}
.detail-modal-title {
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    color: #fff;
}
.detail-modal .van-row {
    margin: 10px 5px;
}
.detail-modal .table-content .van-col:first-child {
    text-align: left;
}
.detail-modal .table-title .van-col:first-child {
    text-align: left;
}
.detail-modal .table-title {
    color: #93969a;
}

.pay-type {
    border-radius: 10px;
    overflow: hidden;
}
.pay-type >>> .van-tabs__line {
    background-color: #0078FF;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    width: 30px!important;
    bottom: 20px;
}
.pay-type >>> .van-tab--active {
    font-weight: bold;
}
.custom-tab-title {
    display: block;
    width: 172px;
    border-right: .5px solid #CDCDCD;
}
.van-collapse-item {
    border-radius: 10px;
    overflow: hidden;
}
</style>



