<template>
  <div>
    <van-form @submit="handleSubmit" :show-error-message="false">
      <!-- 用户类型 -->
      <van-field class="user-type" v-model="userType" name="userType" label="用户类型" center>
        <template #input>
          <base-switch v-model="isChild"></base-switch>
        </template>
      </van-field>
      <!-- 用户名 -->
      <van-field
        name="patName"
        v-model="patName"
        label="姓名"
        label-width="120"
        :placeholder="isChild ? '请输入儿童姓名' : '请输入姓名'"
        :rules="[{ required: true, message: '请输入儿童姓名' }]"
        clearable
      ></van-field>
      <!-- 性别 -->
      <van-field name="patSex" label="性别" label-width="120" v-if="isChild">
        <template #input>
          <van-radio-group v-model="patSex" direction="horizontal">
            <van-radio name="1">男性</van-radio>
            <van-radio name="2">女性</van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <!-- 出生日期 -->
      <div v-if="isChild">
        <van-field
            name="patBirthday"
            v-model="patBirthday"
            label="出生日期"
            label-width="120"
            placeholder="请选择出生日期"
            @click-input="handleSelectDate"
            :rules="[{ required: true, message: '请选择出生日期' }]"
            readonly
        />
        <van-popup v-model="showDatePicker" round position="bottom" :style="{ height: '40%' }">
            <van-datetime-picker
            v-model="currentDate"
            type="date"
            title="选择年月日"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="handleDateComfirm"
            @cancel="handleDateCancel"
            />
        </van-popup>
      </div>
      <!-- 是否本人 -->
      <van-field name="self" v-model="self" label="是否本人" label-width="120">
        <template #input>
          <van-radio-group v-model="self" direction="horizontal">
            <van-radio name="是">是</van-radio>
            <van-radio name="否">否</van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <!-- 关系 -->
      <div v-if="!isSelf">
        <van-field
            name="patRelation"
            v-model="patRelation"
            label="关系"
            label-width="120"
            placeholder="请选择与用户的关系"
            @click="handleSelectRelation"
            :rules="[{ required: true, message: '请选择与用户的关系' }]"
            readonly
        />
        <van-popup v-model="showRelationPicker" round position="bottom">
            <van-picker
            show-toolbar
            :columns="columns"
            @cancel="showRelationPicker = false"
            @confirm="handleConfirm"
            />
        </van-popup>
      </div>
      <!-- 监护人姓名 -->
      <van-field
        name="guardianName"
        v-model="guardianName"
        label="监护人姓名"
        label-width="120"
        placeholder="请输入监护人姓名"
        :rules="[{ required: true, message: '请输入监护人姓名' }]"
        clearable
        v-if="isChild"
      ></van-field>
      <!-- 监护人身份证号 -->
      <van-field
        name="patIdNo"
        type="number"
        v-model="patIdNo"
        :label="isChild ? '监护人身份证号' : '身份证号'"
        :placeholder="isChild ? '请输入监护人身份证号' : '请输入身份证号'"
        label-width="120"
        maxlength="18"
        :rules="[{ required: true, message: '请输入身份证号' }]"
        clearable
      />
      <!-- 监护人手机号 -->
      <van-field
        name="patPhone"
        type="number"
        v-model="patPhone"
        :label="isChild ? '监护人手机号' : '手机号'"
        :placeholder="isChild ? '请输入监护人手机号' : '请输入手机号'"
        label-width="120"
        maxlength="11"
        :rules="[{ required: true, message: '请输入手机号' }]"
        clearable
      />
      <!-- 提示 -->
      <div class="tips">
        <span class="strong">温馨提示：</span><span class="normal">我院实行实名制就诊，请使用本人姓名、身份证号注册，</span><span class="tips-danger">如儿科患者请提供监护人姓名、身份证号和手机号注册，</span><span class="normal">注册的信息将作为就诊的重要参考凭证。</span>
      </div>
      <div class="confirm-btn">
        <van-button round block type="info" :loading="submitLoading" loading-text="请稍后" :disabled="submitDisabled" native-type="submit">新建电子卡</van-button>
      </div>
    </van-form>
    <div class="bottom-tips"><span class="normal">已有就诊卡 </span><span @click="handleNewCard" class="strong">绑定就诊卡</span></div>
  </div>
</template>
<script>
import BSwitch from "../../base/BSwitch";
export default {
    name: "BindCard",
    //注册组件
    components: {
        [BSwitch.name]: BSwitch
    },
    data() {
        return {
            isChild: false,
            patName: "",
            patSex: "1",
            minDate: new Date(1990, 0, 1),
            maxDate: new Date(2020, 10, 1),
            currentDate: new Date(),
            showDatePicker: false,
            patBirthday: "",
            self: "是",
            guardianName: "",
            patRelation: "",
            showRelationPicker: false,
            columns: ["父母", "配偶", "子女", "亲属", "朋友", "同事", "其他"],
            patIdNo: "",
            patPhone: "",
            submitLoading: false,
            submitDisabled: false
        };
    },
    computed: {
        userType() {
            return this.isChild ? 1 : 0;
        },
        isSelf() {
            return this.self == "是";
        }
    },
    methods: {
        handleSelectDate() {
            this.closeAllPicker();
            this.showDatePicker = true;
        },
        handleSelectRelation() {
            this.closeAllPicker();
            this.showRelationPicker = true;
        },
        handleDateCancel() {
            this.showDatePicker = false;
        },
        handleDateComfirm(value) {
            let year = value.getFullYear();
            let month = (Number(value.getMonth()) + 1) + "";
            month = month.length == 1 ? ("0" + month) : month;
            let day = value.getDate() + "";
            day = day.length == 1 ? ("0" + day) : day;
            this.patBirthday = year + "-" + month + "-" + day;
            this.showDatePicker = false;
        },
        handleConfirm(value) {
            this.patRelation = value;
            this.showRelationPicker = false;
        },
        handleSubmit(values) {
            //valid the form of patIdNo and patPhone
            if (!this.validForm(values)) {
                return;
            };
            this.submitLoading = true;
            this.submitDisabled = true;
            let owner = this;
            let data = {};
            let userType = values.userType;
            data["isChild"] = userType;
            data["patRelation"] = values.patRelation || "本人";
            data["patName"] = values.patName;
            data["patIdNo"] = !userType ? values.patIdNo : "";
            data["patPhone"] = values.patPhone;
            data["patSex"] = values.patSex || "";
            data["patBirthday"] = values.patBirthday || "";
            data["guardianIdNo"] = userType ? values.patIdNo : "";
            data["guardianName"] = values.guardianName || "";
            data["guardianPhone"] = userType ? values.patPhone : "";
            owner.globalMethods.setRequestHeader();
            this.$axios({
                            method:'post',
                            url: owner.$store.getters.createCard,
                            data
                        }).then((response) => {
                            if (response.data.Code == 0) {
                                owner.$store.commit("binded", true);
                                owner.toastSuccess("创建成功");
                            }
                            else if(response.data.Code == -2) {
                                this.$toast({
                                    type: "fail",
                                    message: "toke过期，即将重新登录",
                                    forbidClick: true,
                                    onOpened: () => {
                                        owner.globalMethods.delCookie("token");
                                        owner.$router.push("Outpatient");
                                    }
                                });
                            }
                            else {
                                owner.toastFail(response.data.Msg)
                            }
                        })
        },
        handleNewCard() {
            this.$router.replace({name: "BindCard"});
        },
        closeAllPicker() {
            this.showDatePicker = false;
            this.showRelationPicker = false;
        },
        toastFail(msg) {
            this.$toast({
                type: "fail",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.submitDisabled = false;
                    this.submitLoading = false;
                }
            })
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.$router.go(-1);
                }
            })
        },
        validForm(values) {
            if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                this.$toast({
                    type: "fail",
                    message: "身份证号格式错误",
                    forbidClick: true
                });
                return false;
            };
            if(!/^1[3456789]\d{9}$/.test(values.patPhone)) {
                this.$toast({
                    type: "fail",
                    message: "手机号格式错误",
                    forbidClick: true
                });
                return false;
            };
            return true;
        }
    }
};
</script>
<style scoped>
.user-type {
    margin-bottom: 20px;
}
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: blue;
}
.tips-danger {
    color: red;
}
.confirm-btn {
  margin: 16px;
}
.bottom-tips {
    font-size: 12px;
    text-align: right;
    padding: 10px;
    margin: 10px;
    height: 20px;
    line-height: 20px;
}
</style>



