<template>
    <div>
        <div class="title">选择需要就诊的人员</div>
        <div class="card" @click="handleTransferCard(item.patId)" v-for="(item, index) in dataSource" :key="'card' + index">
            <div class="wrap">
                <div class="row">
                    <span class="name">{{item.patName}}</span>
                    <span class="tag">男</span>
                    <span class="tag">{{item.patRelation}}</span>
                    <span class="tag default-card" v-if="item.isDefault == 0">默认就诊卡</span>
                </div>
                <div class="row card-code">
                    <span>卡号 :</span>
                    <span>{{item.patMedNo}}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "CardList",
    data() {
        return {
            dataSource: ""
        };
    },
    mounted() {
        let owner = this;
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        owner.globalMethods.setRequestHeader();
        this.$axios({
                method:'post',
                url: owner.$store.getters.queryCardInfo,
                data: {patId: ""}
            }).then ((response) => {
                if (response.data.Code == 0) {
                    owner.dataSource = response.data.Data;
                    owner.$toast.clear();
                }
                else if (response.data.Code == -2) {
                    owner.$toast({
                        type: "fail",
                        message: "toke过期，即将重新登录",
                        forbidClick: true,
                        onOpened: () => {
                            owner.globalMethods.delCookie("token");
                            owner.$router.push("Outpatient");
                        }
                    });
                }
                else {
                    owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: true
                        })
                }
            })
    },
    methods: {
        handleTransferCard(patId) {
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                    method:'post',
                    url: owner.$store.getters.updateCardInfo,
                    data: {patId: patId, isDefault: 0}
                }).then((response) => {
                    if (response.data.Code == 0) {
                        owner.$router.go(-1);
                    }
                    else if (response.data.Code == -2) {
                        owner.$toast({
                            type: "fail",
                            message: "toke过期，即将重新登录",
                            forbidClick: true,
                            onOpened: () => {
                                owner.globalMethods.delCookie("token");
                                owner.$router.push("Outpatient");
                            }
                        });
                    }
                    else {
                        owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: true
                        })
                    }
                })
        }
    }
};

</script>
<style scoped>
.title {
    text-align: left;
    margin: 10px;
    font-size: 15px;
    color: #716f6f;
}
.card {
    width: 97%;
    background-color: #1830b4;
    border-radius: 15px;
    padding: 15px;
    margin: 5px auto 0 auto;
    box-sizing: border-box;
}
.card .wrap {
    height: 100%;
}
.wrap .row {
    text-align: left;
    color: #fff;
    margin-bottom: 10px;
}
.wrap .name {
    margin-right: 20px;
}
.wrap .tag {
    background-color: #0c41d6;
    font-size: 12px;
    display: inline-block;
    padding: 2px 4px;
    border-radius: 2px;
    margin-right: 4px;
}
.wrap .card-code {
    font-size: 14px;
    margin-bottom: 0px;
}
.default-card {
    float: right;
}


</style>



