<template>
    <div class="container">
        <div class="wrapper" v-for="(item, index) in dataSource" :key="'reg' + index">
            <div class="content">
                <van-row class="order-state">
                    <van-col class="size1" span="20">订单号：{{item.serialNo}}</van-col>
                    <van-col class="color1" span="4">{{item.statusLabel}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <van-icon :name="require('../../../assets/images/img/dd-jiuzhenren.png')" />
                        <span>就诊人</span>
                    </van-col>
                    <van-col span="12">{{item.patName}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <van-icon :name="require('../../../assets/images/img/dd-yisheng.png')" />
                        <span>就诊医生</span>
                    </van-col>
                    <van-col span="12">{{item.doctorName}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <van-icon :name="require('../../../assets/images/img/dd-keshi.png')" />
                        <span>就诊科室</span>
                    </van-col>
                    <van-col span="12">{{item.deptName}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <van-icon :name="require('../../../assets/images/img/dd-riqi.png')" />
                        <span>就诊时间</span>
                    </van-col>
                    <van-col span="12">{{item.appointDate.slice(0, 10)}} {{item.timeflagLabel}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <van-icon :name="require('../../../assets/images/img/dd-yymm.png')" />
                        <span class="color2">预约密码</span>
                    </van-col>
                    <van-col class="c   olor2" span="12">{{item.passWord}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <van-icon :name="require('../../../assets/images/img/dd-shijian.png')" />
                        <span>订单时间</span>
                    </van-col>
                    <van-col span="12">{{item.operateDate.slice(0, 10)}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <van-icon :name="require('../../../assets/images/img/dd-feiyong.png')" />
                        <span>挂号费</span>
                    </van-col>
                    <van-col class="color3" span="12">{{item.totalFee}}元</van-col>
                </van-row>
                <div class="btn" v-if="item.status == 0"><span @click="handleCancelAppoint(index)">取消预约</span></div>
            </div>
        </div>
        <van-empty description="暂无数据" v-if="dataSource.length == 0"/>
    </div>
</template>
<script>
export default {
    name: "MyRegister",
    data() {
        return {
            dataSource: ""
        };
    },
    mounted() {
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        let owner = this;
        let patId = this.$route.query.patId;
        let patMedNo = this.$route.query.patMedNo;
        owner.globalMethods.setRequestHeader();
        this.$axios({
                        method:'post',
                        url: owner.$store.getters.queryAppointRegisterList,
                        data: {patId, patMedNo}
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            owner.dataSource = owner.wrapDataSource(response.data.Data);
                            owner.$toast.clear();
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.$toast({
                                    type: "fail",
                                    message: response.data.Msg,
                                    forbidClick: true
                                })
                        }
                    })
    },
    methods: {
        handleCancelAppoint(index) {
            this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
            let data = {};
            let curData = this.dataSource[index];
            data["patId"] = curData.patId;
            data["patName"] = curData.patName;
            data["patMedNo"] = curData.patMedNo;
            data["deptNo"] = curData.deptNo;
            data["doctorNo"] = curData.doctorNo;
            data["timeFlag"] = curData.timeFlag;
            data["appointDate"] = curData.appointDate.slice(0, 10);
            data["appointTime"] = "";
            data["type"] = 1;
            data["passWord"] = curData.passWord;
            data["clinicType"] = 0;
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                        method:'post',
                        url: owner.$store.getters.appointRegisterOrCancel,
                        data
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            owner.$toast.clear();
                            curData.status = 2;
                            curData.statusLabel = "已取消";
                            this.$dialog({ message: "取消预约成功" });
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.$toast({
                                    type: "fail",
                                    message: response.data.Msg,
                                    forbidClick: true,
                                    onOpened: () => {
                                        owner.submitDisabled = false;
                                        owner.submitLoading = false;
                                    }
                                });
                        }
                    })
        },
        wrapDataSource(data) {
            for (let i = 0; i < data.length; i++) {
                let curItem = data[i];
                curItem.statusLabel = this.$store.state.locale["register-status-" + curItem.status];
                curItem.timeflagLabel = this.$store.state.locale["timeflag-" + curItem.timeFlag];
                
            }
            return data;
        }
    }
};

</script>
<style scoped>
.container {
    min-height: 100vh;
    background-color: #F3F5F9;
    padding: 10px 15px;
}
.wrapper {
    margin-bottom: 10px;
    border-radius: 10px;
    overflow: hidden;
}
.content {
    background-color: #fff;
    padding: 10px 15px;
}
.content .van-row .van-col{
    height: 30px;
    line-height: 30px;
    padding: 5px 0;
    font-size: 14px;
}
.content .van-row.order-state .van-col{
    height: 40px;
    border-bottom: 0.5px solid #e2d7d7;
}
.content .van-row .van-col .van-icon {
    margin-right: 15px;
}
.content .van-row .van-col:first-child {
    display: flex;
    align-items: center;
    color: #6d6868;
}
.content .van-row .van-col:last-child {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #000;
}
.content .btn {
    display: flex;
    justify-content: flex-end;
    padding: 10px 0;
    border-top: 0.5px solid #e2d7d7;
}
.content .btn>span {
    width: 70px;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    color: #fff;
    padding: 5px;
    border-radius: 3px;
    font-size: 14px;
}
.size1 {
    font-size: 14px !important;
}
.color1 {
    color:#ce8e1b !important;
}
.color2 {
    color: #ff6257 !important;
}
.color3 {
    color:#ff6257 !important;
}
</style>



