<template>
    <div class="container">
        <van-form @submit="handleSubmit" :show-error-message="false">
        <!-- 用户类型 -->
        <van-field v-model="userType" name="userType" v-show="false"></van-field>
        <van-tabs 
            class="user-type" 
            v-model="isChild"
            title-inactive-color="#818594" 
            title-active-color="#292C34"
            >
            <van-tab title="成人">
                <template #title>
                    <span class="custom-tab-title">成人</span>
                </template>
            </van-tab>
            <van-tab title="儿童"></van-tab>
        </van-tabs>
        <div class="section">
            <!-- 用户名 -->
            <van-field
                name="patName"
                v-model="patName"
                label="姓名"
                label-width="120"
                :placeholder="isChild ? '请输入儿童姓名' : '请输入姓名'"
                :rules="[{ required: true, message: '请输入儿童姓名' }]"
                clearable
            ></van-field>
            <!-- 性别 -->
            <van-field
                name="patSex"
                label="性别"
                label-width="120"
                v-if="isChild"
                >
                    <template #input>
                        <van-radio-group v-model="patSex" direction="horizontal">
                            <van-radio name="1">男性</van-radio>
                            <van-radio name="2">女性</van-radio>
                        </van-radio-group>
                    </template>
            </van-field>
            <!-- 出生日期 -->
            <div v-if="isChild">
                <van-field
                    name="patBirthday"
                    v-model="patBirthday"
                    label="出生日期"
                    label-width="120"
                    placeholder="请选择出生日期"
                    @click-input="handleSelectDate"
                    :rules="[{ required: true, message: '请选择出生日期' }]"
                    readonly
                />
                <van-popup v-model="showDatePicker" round position="bottom" :style="{ height: '40%' }">
                    <van-datetime-picker
                    v-model="currentDate"
                    type="date"
                    title="选择年月日"
                    :min-date="minDate"
                    :max-date="maxDate"
                    @confirm="handleDateComfirm"
                    @cancel="handleDateCancel"
                    />
                </van-popup>
            </div>
            <!-- 关系 -->
            <div v-if="!isSelf">
                <van-field
                    name="patRelation"
                    v-model="patRelation"
                    label="关系"
                    label-width="120"
                    placeholder="请选择与用户的关系"
                    @click="handleSelectRelation"
                    :rules="[{ required: true, message: '请选择与用户的关系' }]"
                    readonly
                />
                <van-popup v-model="showRelationPicker" round position="bottom">
                    <van-picker
                    show-toolbar
                    :columns="columns"
                    @cancel="showRelationPicker = false"
                    @confirm="handleConfirm"
                    />
                </van-popup>
            </div>
            <!-- 监护人姓名 -->
            <van-field
                name="guardianName"
                v-model="guardianName"
                label="监护人姓名"
                label-width="120"
                placeholder="请输入监护人姓名"
                :rules="[{ required: true, message: '请输入监护人姓名' }]"
                clearable
                v-if="isChild"
            ></van-field>
            <!-- 监护人身份证号 -->
            <van-field
                name="patIdNo"
                v-model="patIdNo"
                :label="isChild ? '监护人身份证号' : '身份证号'"
                :placeholder="isChild ? '请输入监护人身份证号' : '请输入身份证号'"
                label-width="120"
                maxlength="18"
                :rules="[{ required: true, message: '请输入身份证号' }]"
                clearable
            />
            <!-- 监护人手机号 -->
            <van-field
                name="patPhone"
                type="number"
                v-model="patPhone"
                :label="isChild ? '监护人手机号' : '手机号'"
                :placeholder="isChild ? '请输入监护人手机号' : '请输入手机号'"
                label-width="120"
                maxlength="11"
                :rules="[{ required: true, message: '请输入手机号' }]"
                clearable
            />
        </div>
        <div class="section">
            <!-- 是否本人 -->
            <van-field 
                name="switch" 
                label="是否本人" 
                label-width="120"
                >
                    <template #input>
                        <van-switch v-model="isSelf" size="20" />
                    </template>
            </van-field>
        </div>
      <!-- 提示 -->
      <div class="tips">
        <p class="normal m-b-5">温馨提示：</p>
        <p class="normal">1、若您还没有就诊卡，本次添加后将使用您提供的 <span class="danger">姓名、身份证号</span> 为您注册。</p>
        <p class="normal">2、若您已持有就诊卡，请确保<span class="danger">就诊卡内记录有您的身份证号（先到窗口补录身份证后再绑卡）</span> ，不要随意注册档案，否则可能导致您的病例信息丢失。</p>
      </div>
      <div class="confirm-btn">
        <van-button block color="linear-gradient(to right, #00CAFF , #2772FF)" :loading="submitLoading" loading-text="请稍后" :disabled="submitDisabled" native-type="submit">添加就诊人</van-button>
      </div>
    </van-form>
    <van-popup class="select-card-modal" v-model="showPopup" @close="handleClosePopup">
        <div class="popup pr">
            <div class="title">该用户有多张就诊卡，请选择要绑定的就诊卡</div>
            <div class="content">
                <div class="card" v-for="(item, index) in cardList" :key="'card' + index" @click="bindCard(item.patId)">
                    <div class="wrap">
                        <div class="row">
                            <span class="name">{{item.patName}}</span>
                        </div>
                        <div class="row card-code">
                            <span>卡号 :</span>
                            <span>{{item.patMedNo}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </van-popup>
  </div>
</template>
<script>
export default {
    name: "BindCard",
  //注册组件
    components: {

    },
    data() {
        return {
            isChild: 0,
            patName: "",
            patSex: "1",
            minDate: new Date(1990, 0, 1),
            maxDate: new Date(2020, 10, 1),
            currentDate: new Date(),
            showDatePicker: false,
            patBirthday: "",
            isSelf: true,
            guardianName: "",
            patRelation: "",
            showRelationPicker: false,
            columns: ["父母", "配偶", "子女", "亲属", "朋友", "同事", "其他"],
            patIdNo: "",
            patPhone: "",
            showPopup: false,
            submitLoading: false,
            submitDisabled: false,
            cardList: [],
            formData: {},
            hisDataList: []
        };
    },
    computed: {
        userType() {
            return this.isChild;
        }
    },
    methods: {
        handleSubmit(values) {
            let formData = values;
            //valid the form of patIdNo and patPhone
            if (!this.validForm(formData)) {
                return;
            };
            this.formData = formData;
            this.submitLoading = true;
            this.submitDisabled = true;
            let owner = this;
            let data = {};
            let userType = formData.userType;
            data["patName"] = formData.patName;
            if (!userType) {
                data["patIdNo"] = formData.patIdNo;
                // data["patPhone"] = formData.patPhone;
            }
            else {
                data["guardianIdNo"] = formData.patIdNo;
            }
            owner.globalMethods.setRequestHeader();
            this.$axios({
                        method:'post',
                        url: owner.$store.getters.queryCardList,
                        data
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            let resData = response.data.Data;
                            owner.hisDataList = resData;
                            if (resData.length == 1) {
                                let patId = resData[0].patId;
                                owner.bindCard(patId);
                            }
                            else {
                                owner.cardList = resData;
                                owner.showPopup = true;
                            }
                        }
                        else if (response.data.Code == 1) {
                            owner.newCard();
                        }
                        else if (response.data.Code == -2) {
                                owner.$toast({
                                    type: "fail",
                                    message: "toke过期，即将重新登录",
                                    forbidClick: true,
                                    onOpened: () => {
                                        owner.globalMethods.delCookie("token");
                                        owner.$router.push("Outpatient");
                                    }
                                });
                        }
                        else {
                            owner.toastFail(response.data.Msg);
                        }
                    })
        },
        handleSelectDate() {
            this.closeAllPicker();
            this.showDatePicker = true;
        },
        handleSelectRelation() {
            this.closeAllPicker();
            this.showRelationPicker = true;
        },
        handleDateCancel() {
            this.showDatePicker = false;
        },
        handleDateComfirm(value) {
            let year = value.getFullYear();
            let month = (Number(value.getMonth()) + 1) + "";
            month = month.length == 1 ? ("0" + month) : month;
            let day = value.getDate() + "";
            day = day.length == 1 ? ("0" + day) : day;
            this.patBirthday = year + "-" + month + "-" + day;
            this.showDatePicker = false;
        },
        handleConfirm(value) {
            this.patRelation = value;
            this.showRelationPicker = false;
        },
        handleClosePopup() {
            this.submitLoading = false;
            this.submitDisabled = false;
        },
        bindCard(patId) {
            let owner = this;
            let formData = this.formData;
            let hisData;
            for (let i = 0; i < owner.hisDataList.length; i++) {
                let item = owner.hisDataList[i];
                if (item.patId == patId) {
                    hisData = item;
                    break;
                }
            };
            let data = owner.getParams(hisData, formData);
            data["patId"] = patId;
            data["patMedNo"] = hisData.patMedNo;
            owner.globalMethods.setRequestHeader();
                owner.$axios({
                    method: "post",
                    url: owner.$store.getters.bindCard,
                    data: data
                }).then((response) => {
                    if (response.data.Code == 0) {
                        owner.$store.commit("binded", true);
                        owner.toastSuccess("绑定成功");
                    }
                    else if (response.data.Code == -2) {
                        owner.$toast({
                            type: "fail",
                            message: "toke过期，即将重新登录",
                            forbidClick: true,
                            onOpened: () => {
                                owner.globalMethods.delCookie("token");
                                owner.$router.push("Outpatient");
                            }
                        });
                    }
                    else {
                        owner.toastFail(response.data.Msg);
                    }
                })
        },
        newCard() {
            let owner = this;
            owner.globalMethods.setRequestHeader();
            let data = owner.getParams("", owner.formData);
            this.$axios({
                            method:'post',
                            url: owner.$store.getters.createCard,
                            data
                        }).then((response) => {
                            if (response.data.Code == 0) {
                                owner.$store.commit("binded", true);
                                owner.toastSuccess("创建成功");
                            }
                            else if(response.data.Code == -2) {
                                this.$toast({
                                    type: "fail",
                                    message: "toke过期，即将重新登录",
                                    forbidClick: true,
                                    onOpened: () => {
                                        owner.globalMethods.delCookie("token");
                                        owner.$router.push("Outpatient");
                                    }
                                });
                            }
                            else {
                                owner.toastFail(response.data.Msg)
                            }
                        })
        },
        getParams(hisData, formData) {
            let data = {};
            let userType = formData.userType;
            data["isChild"] = userType;
            data["patRelation"] = hisData.patRelation || formData.patRelation || "本人";
            data["patName"] = hisData.patName || formData.patName || "";
            data["patIdNo"] = !userType ? (hisData.patIdNo || formData.patIdNo) : "";
            data["patPhone"] = !userType ? (hisData.patPhone || formData.patPhone) : "";
            data["patSex"] = hisData.patSex || formData.patSex || "";
            data["patBirthday"] = hisData.patBirthday || formData.patBirthday || "";
            data["guardianName"] = hisData.guardianName || formData.guardianName || "";
            data["guardianIdNo"] = userType ? (hisData.guardianIdNo || formData.patIdNo) : "";
            data["guardianPhone"] = userType ? (hisData.guardianPhone || formData.patPhone) : "";
            return data;
        },
        validForm(values) {
            if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                this.$toast({
                    type: "fail",
                    message: "身份证号格式错误",
                    forbidClick: true
                });
                return false;
            }
            if(!/^1[3456789]\d{9}$/.test(values.patPhone)) {
                this.$toast({
                    type: "fail",
                    message: "手机号格式错误",
                    forbidClick: true
                });
                return false;
            };
            return true;
        },
        closeAllPicker() {
            this.showDatePicker = false;
            this.showRelationPicker = false;
        },
        toastFail(msg) {
            this.$toast({
                type: "fail",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.submitDisabled = false;
                    this.submitLoading = false;
                }
            })
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.$router.go(-1);
                }
            })
        }
    }
};
</script>
<style scoped>
.container {
    height: 100vh;
    background-color: #F4F6F9;
    padding: 10px 15px;
}
.container >>> .van-cell::after {
    right: 16px;
}
.user-type {
    border-radius: 10px;
    overflow: hidden;
}
.user-type >>> .van-tabs__line {
    background-color: #0078FF;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    width: 30px!important;
    bottom: 20px;
}
.user-type >>> .van-tab--active {
    font-weight: bold;
}
.custom-tab-title {
    display: block;
    width: 172px;
    border-right: .5px solid #CDCDCD;
}
.section {
    border-radius: 10px;
    margin-top: 10px;
    overflow: hidden;
}
.tips {
    padding: 20px 0 30px 0;
    color: #494D5A;
    font-size: 12px;
    text-align: left;
}
.normal {
    line-height: 20px;
}
.danger {
    color: #FF6257;
}
.confirm-btn {
    height: 43px;
    border-radius: 10px;
    overflow: hidden;
}
.confirm-btn >>> .van-button__text {
    font-size: 16px;
}
.select-card-modal {
    z-index: 2002;
    width: 90%;
    border-radius: 10px;
    overflow: hidden;
}
.popup .title {
    width: 100%;
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    position: fixed;
    top: 0;
    left: 0;
}
.popup .content {
    margin-top: 40px;
    max-height: 400px;
    overflow: scroll;
    background-color: #F4F6F9;
    padding-bottom: 10px;
}
.popup .card {
    width: 93%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.popup .card .wrap {
    height: 100%;
}
.popup .wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.popup .wrap .name {
    color: #292C34;
}
.popup .wrap .card-code {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}

</style>



