<template>
    <div class="container">
        <div>
            <van-cell-group class="card-item" v-for="(item, index) in dataSource" :key="'list' + index">
                <van-cell title-style="text-align: left;" title="姓名" :value="item.patName" />
                <van-cell title-style="text-align: left;" title="科室" :value="item.execDeptName"/>
                <van-cell title-style="text-align: left;" title="类目" :value="item.itemName"/>
                <van-cell title-style="text-align: left;" title="规格" :value="item.spec"/>
                <van-cell title-style="text-align: left;" title="单价" :value="Number(item.price).toFixed(2) + '元'"/>
                <van-cell title-style="text-align: left;" title="数量" :value="Number(item.number).toFixed(2)"/>
                <van-cell title-style="text-align: left;" title="总价" :value="Number(item.consume).toFixed(2) + '元'"/>
            </van-cell-group>
            <van-empty description="暂无数据" v-if="dataSource.length == 0"/>
        </div>
    </div>
</template>
<script>
export default {
    name: "DayList",
    data() {
        return {
            dataSource: "",
            startDate: "",
            endDate: ""
        };
    },
    mounted() {
        this.getDate();
        let owner = this;
        owner.globalMethods.setRequestHeader();
        this.$axios({
                    method:'post',
                    url: owner.$store.getters.queryDayList,
                    data: {
                        startDate: this.startDate,
                        endDate: this.endDate
                    }
                }).then ((response) => {
                    if (response.data.Code == 0) {
                        owner.dataSource = response.data.Data;
                        owner.$toast.clear();
                    }
                    else if (response.data.Code == -2) {
                        owner.$toast({
                            type: "fail",
                            message: "toke过期，即将重新登录",
                            forbidClick: true,
                            onOpened: () => {
                                owner.globalMethods.delCookie("token");
                                owner.$router.push("Outpatient");
                            }
                        });
                    }
                    else {
                        owner.$toast({
                                type: "fail",
                                message: response.data.Msg,
                                forbidClick: true
                            })
                    }
                });
    },
    methods: {
        getDate() {
            let date = this.globalMethods.getDate(0);
            this.startDate = date.year + "-" + date.month + "-" + date.day;
            this.endDate = this.startDate;
        }
    }
};

</script>
<style scoped>
.container {
    min-height: 100vh;
    background-color: #F3F5F9;
    padding: 10px 15px;
}
.card-item {
    margin-bottom: 15px;
    border-radius: 10px;
    overflow: hidden;
}
</style>



