<template>
    <div class="container">
        <ul class="date-container">
            <li v-for="(item, index) in dateList" :key="index" @click="hanldeSelectDate(index)" :class="{active: index == curSelect}">
                <span>{{item.week}}</span>
                <span>{{item.day}}</span>
            </li>
        </ul>
        <div class="title" v-if="dataSource.length > 0">坐诊医生列表</div>
        <van-row class="doctor-list" v-for="(item, index) in dataSource" :key="'doc' + index">
            <van-col class="pic" span="6">
                <van-image width="70" height="70" round :src="item.doctorAvatar" />
            </van-col>
            <van-col class="des" span="14">
                <div class="strong">{{item.doctorName}}</div>
                <div>所属科室：{{item.deptName}}</div>
                <div>剩余号源：<span class="strong">{{item.appointAvailableNum}}</span>个</div>
            </van-col>
            <van-col class="btn" span="4" @click="handleGoDoctorDetail(item.doctorNo)">
                <van-icon size="20" :name="require('../../../assets/images/img/gh-rq.png')" />
                <span>预约</span>
            </van-col>
        </van-row>
        <van-empty description="" v-if="dataSource.length == 0" />
    </div>
</template>
<script>
export default {
    name: "DoctorList",
    data() {
        return {
            dateList: "",
            curSelect: 0,
            dataSource: ""
        };
    },
    created() {
        let dateList = [];
        for (let i = 1; i < 8; i++) {
            dateList.push(this.getDate(i));
        }
        this.dateList = dateList;
    },
    mounted() {
        this.hanldeSelectDate(0);
    },
    methods: {
        getDate(add = 0) {
            let currDate = new Date();
            let newDateMs = currDate.getTime() + add*86400000;
            let newDate = new Date(newDateMs);
            let year = newDate.getFullYear() + "";
            let month = newDate.getMonth() + 1;
            month = (month + "").length == 1 ? ("0" + month) : (month + "");
            let day = newDate.getDate();
            day = (day + "").length == 1 ? ("0" + day) : (day + "");
            let weekNum = newDate.getDay();
            let week = "";
            switch (weekNum) {
                case 0:
                    week = "日";
                    break;
                case 1:
                    week = "一";
                    break;
                case 2:
                    week = "二";
                    break;
                case 3:
                    week = "三";
                    break;
                case 4:
                    week = "四";
                    break;
                case 5:
                    week = "五";
                    break;
                case 6:
                    week = "六";
                    break;
            };
            return {year, month, day, week};
        },
        hanldeSelectDate(value) {
            this.$toast.loading({message: '加载中...', forbidClick: false, duration: 0});
            this.curSelect = value;
            let dateObj = this.getDate(value + 1);
            let date = dateObj.year + "-" + dateObj.month + "-" + dateObj.day;
            let owner = this;
            let deptNo = this.$route.query.deptNo;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                            method:'post',
                            url: owner.$store.getters.getDoctorList,
                            data: {deptNo, date}
                        }).then((response) => {
                            if (response.data.Code == 0) {
                                owner.dataSource = response.data.Data;
                                owner.$toast.clear();
                            }
                            else if (response.data.Code == -2) {
                                owner.$toast({
                                    type: "fail",
                                    message: "toke过期，即将重新登录",
                                    forbidClick: true,
                                    onOpened: () => {
                                        owner.globalMethods.delCookie("token");
                                        owner.$router.push("Outpatient");
                                    }
                                });
                            }
                            else {
                                owner.$toast({
                                    type: "fail",
                                    message: response.data.Msg,
                                    forbidClick: false
                                });
                                owner.dataSource = "";
                            }
                        })
                        
        },
        handleGoDoctorDetail(doctorNo) {
            let deptNo = this.$route.query.deptNo;
            this.$router.push({name: "DoctorDetail", query: {deptNo, doctorNo}});
        }
    }
};

</script>
<style scoped>
.container {
    min-height: 100vh;
    background-color: #F3F5F9;
    padding: 10px 15px;
}
.date-container {
    width: 100%;
    display: flex;
    background-color: #fff;
    border-radius: 10px;
    overflow: hidden;
}
.date-container li{
    width: 14.28%;
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 5px 0;
    color: #0078FF;
}
.date-container .active {
    background-color: #0078FF;
    color: #fff;
}
.title {
    text-align: left;
    font-size: 15px;
    color: #514c4c;
    padding: 10px;
}
.doctor-list {
    background-color: #fff;
    padding: 0px 10px;
    height: 100px;
    margin-bottom: 10px;
    border-radius: 10px;
    overflow: hidden;
}
.doctor-list .van-col {
    height: 100%;
    padding: 15px 0;
}
.doctor-list .pic {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.doctor-list .des {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: left;
    font-size: 14px;
    color: #88929d;
    padding-left: 10px;
    border-right: 0.5px solid #d4d8dd;
}
.doctor-list .des .strong {
    color: black;
}
.doctor-list .btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 14px;
    color: #0078FF;
}
</style>



