<template>
    <div class="main-bg">
        <div class="radius-10">
            <div class="title linear">{{dataSource.reportName}}</div>
            <van-cell title-style="text-align: left;" title="姓名" :value="dataSource.patName" />
            <van-cell title-style="text-align: left;" title="性别" :value="dataSource.patSex && (dataSource.patSex == 1 ? '男' : '女')" />
            <van-cell title-style="text-align: left;" title="年龄" :value="dataSource.patAge && (dataSource.patAge + '岁')" />
            <van-cell title-style="text-align: left;" title="申请科室" :value="dataSource.deptName" />
            <van-cell title-style="text-align: left;" title="临床诊断" :value="dataSource.diagnosis" v-if="type == 'inspe'"/>
            <van-cell title-style="text-align: left;" title="报告时间" :value="dataSource.reportDate" />
            <van-cell title-style="text-align: left;" title="报告医生" :value="dataSource.doctorName" />
            <van-cell title-style="text-align: left;" title="报告审核人" :value="dataSource.reportAutitor" v-if="type == 'check'"/>
            <van-cell title-style="text-align: left;" value-class="text-left" title="检查所见" :value="dataSource.findings" v-if="type == 'check'"/>
            <van-cell title-style="text-align: left;" value-class="text-left" title="诊断信息" :value="dataSource.diagnostic" v-if="type == 'check'"/>
        </div>
        <div class="grid" v-if="dataSource.details">
            <van-row class="row">
                <van-col span="3">序号</van-col>
                <van-col span="8">项目名称</van-col>
                <van-col span="4">结果</van-col>
                <van-col span="4">单位</van-col>
                <van-col span="5">参考值</van-col>
            </van-row>
            <van-row class="row" v-for="(item, index) in dataSource.details" :key="'row' + index">
                <van-col span="3">{{index+1}}</van-col>
                <van-col span="8">{{item.itemName}}</van-col>
                <van-col span="4">{{item.result}}</van-col>
                <van-col span="4">{{item.unit}}</van-col>
                <van-col span="5">{{item.context}}</van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
export default {
    name: "ReportDetail",
    data() {
        return {
            dataSource: "",
            type: ""
        };
    },
    mounted() {
        let type = this.$route.query.type;
        let data = this.$route.params.data;
        this.type = type;
        if (type == "check") {
            this.dataSource = data;
        }
        else {
            this.queryReportDetail();
        }
    },
    methods: {
        queryReportDetail() {
            let applyNo = this.$route.query.applyNo;
            let owner = this;
            this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
            owner.globalMethods.setRequestHeader();
            this.$axios({
                    method:'post',
                    url: owner.$store.getters.queryReportDetail,
                    data: { applyNo }
                }).then ((response) => {
                    if (response.data.Code == 0) {
                        owner.dataSource = response.data.Data;
                        setTimeout(() => {owner.perfectGrid();}, 100);
                        owner.$toast.clear();
                    }
                    else if (response.data.Code == -2) {
                        owner.$toast({
                            type: "fail",
                            message: "toke过期，即将重新登录",
                            forbidClick: true,
                            onOpened: () => {
                                owner.globalMethods.delCookie("token");
                                owner.$router.push("Outpatient");
                            }
                        });
                    }
                    else {
                        owner.$toast({
                                type: "fail",
                                message: response.data.Msg,
                                forbidClick: true
                            })
                    }
                });
        },
        perfectGrid() {
            let rowEle = $(".row");
            for (let i = 0; i < rowEle.length; i++) {
                let item = rowEle.eq(i);
                let height = item.height();
                let cols = item.find(".van-col");
                for (let k = 0; k < cols.length; k++) {
                    cols.eq(k).css("height", height);
                }
            };
        }
    }
};

</script>
<style scoped>
.title {
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    background-color: #0078ff;
}
.grid {
    width: 95%;
    margin: 10px auto;
    padding: 0 1px 1px 0;
    border-bottom: 0.5px solid #a5a5a5;
    border-right: 0.5px solid #a5a5a5;
}
.grid .van-row:nth-child(2n-1) {
    background-color: #ececec;
}
.grid .van-row:nth-child(2n) {
    background-color: #fff;
}
.grid .van-row .van-col {
    min-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 0.5px solid #a5a5a5;
    border-left: 0.5px solid #a5a5a5;
}
.text-left {
    text-align: left;
}
</style>



