<template>
    <div class="charge main-bg">
          <van-field
            class="radius-10"
            type="number"
            v-model="code"
            placeholder="请输入他人就诊卡号"
            center
            clearable
            >
            </van-field>
            <div class="tips">
                <span class="strong">温馨提示：</span><span>请输入患者相关信息，并再准确核对充值信息后，再执行充值操作！</span>
            </div>
            <div style="margin: 16px;">
                <van-button round block type="info" :disabled="submitDisabled" :loading="submitLoading" native-type="submit" @click="handleGoCharge">确定</van-button>
            </div>
            <div class="bottom-tips"><span>不知道就诊卡号 </span><span class="strong" @click="handleGoQueryOthersCardByName">查询</span></div>
    </div>
</template>

<script>
export default {
    name: "QueryOthersCardByNum",
    data() {
        return {
            type: "",
            code: "",
            submitDisabled: false,
            submitLoading: false
        };
    },
    mounted() {
        this.type = this.$route.query.type;
    },
    methods: {
        handleGoCharge() {
            if (this.code.length == 0) {
                this.$toast({
                    type: "fail",
                    message: "请输入就诊卡号",
                    forbidClick: true
                });
                return;
            }
            this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                            method:'post',
                            url: owner.$store.getters.queryCardInfoById,
                            data: {
                                code: owner.code,
                                type: 1
                            }
                        }).then((response) => {
                            if (response.data.Code == 0) {
                                let patId = response.data.Data[0].patId;
                                owner.$router.replace({name: owner.type, query: { patId }});
                            }
                            else if (response.data.Code == -2) {
                                owner.$toast({
                                    type: "fail",
                                    message: "toke过期，即将重新登录",
                                    forbidClick: true,
                                    onOpened: () => {
                                        owner.globalMethods.delCookie("token");
                                        owner.$router.push("Outpatient");
                                    }
                                });
                            }
                            else {
                                owner.toastFail(response.data.Msg)
                            }
                        })
        },
        handleGoQueryOthersCardByName() {
            let type = this.type;
            this.$router.replace({name: "QueryOthersCardByName", query: { type }});
        },
        toastFail(msg) {
            this.$toast({
                type: "fail",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.submitDisabled = false;
                    this.submitLoading = false;
                }
            })
        }
    }
};

</script>
<style scoped>
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: #0078ff;
}
.bottom-tips {
    font-size: 12px;
    text-align: right;
    padding: 10px;
    margin: 10px;
    height: 20px;
    line-height: 20px;
}

</style>



