<template>
    <div>
        <com-card @show-code="handleOnGoShowCode" :status="binded" :type="0" :data="dataSource"></com-card>
        <div class="main m-t-70 p-l-15 p-r-15">
            <div class="main-service p-b-15">
                <base-operate-bar :text="{firstTxt:'常用', secondTxt: '服务'}"></base-operate-bar>
                <van-grid :border="false" :column-num="4">
                    <van-grid-item>
                        <base-service-btn :text="'本人充值'" :icon="require('../../../assets/images/charge.png')" :gradient="'#00CAFF,#008CFF'" @click.native="handldeGoPersonalPay('Charge')"></base-service-btn>
                    </van-grid-item>
                    <van-grid-item>
                        <base-service-btn :text="'本人缴费'" :icon="require('../../../assets/images/pay.png')" :gradient="'#9DEB7E,#61C854'" @click.native="handldeGoPersonalPay('Pay')"></base-service-btn>
                    </van-grid-item>
                    <van-grid-item>
                        <base-service-btn :text="'预约挂号'" :icon="require('../../../assets/images/outpatient/appoing.png')" :gradient="'#FFD418,#FDB404'" @click.native="handleGoAppointRegister"></base-service-btn>
                    </van-grid-item>
                    <van-grid-item>
                        <base-service-btn :text="'就诊人管理'" :icon="require('../../../assets/images/outpatient/manage_card.png')" :gradient="'#6BDF6B,#3FD18B'" @click.native="handleGoDetailPage('ManageCard')"></base-service-btn>
                    </van-grid-item>
                </van-grid>
            </div>
            <div class="more-service">
                <base-operate-bar :text="{firstTxt:'更多', secondTxt: '服务'}"></base-operate-bar>
                <div class="more-service flex_dom flex_item_between m-t-10">
                    <div class="left">
                        <base-service-card :scale="'large'" :icon="require('../../../assets/images/outpatient/more_service01.png')" :title="'我的挂号'" :title-desc="'查看挂号成功信息'" @click.native="handleGoMyRegister('MyRegister')"></base-service-card>
                    </div>
                    <div class="right flex_column flex_item_between">
                        <div class="hgt80">
                            <base-service-card :scale="'middle'" :icon="require('../../../assets/images/outpatient/more_service02.png')" :title="'为他人充值'" :title-desc="'为他人的就诊卡充值'" @click.native="handleGoOthersCharge('QueryOthersCardByNum')"></base-service-card>
                        </div>
                        <div class="hgt80">
                            <base-service-card :scale="'middle'" :icon="require('../../../assets/images/outpatient/more_service03.png')" :title="'为他人缴费'" :title-desc="'为他人的医嘱项目缴费'" @click.native="handleGoOthersPay('QueryOthersCardByNum')"></base-service-card>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import CCard from "../../common/CCard";
import BOperateBar from "../../base/BOperateBar";
import BServiceBtn from "../../base/BServiceBtn";
import BServiceCard from "../../base/BServiceCard";
export default {
    name: "Outpatient",
    components: {
        [CCard.name]: CCard,
        [BOperateBar.name]: BOperateBar,
        [BServiceBtn.name]: BServiceBtn,
        [BServiceCard.name]: BServiceCard,
    },
    data() {
        return {
            border: false,
            dataSource: {},
        };
        
    },
    mounted() {
        this.setToken();
    },
    computed: {
        binded() {
            return this.$store.state.binded;
        }
    },
    methods: {
        handleOnGoShowCode() {
            this.$router.push({name: "CodeDetail"});
        },
        handleGoBindCard() {
            this.$router.push({name: "BindCard"});
        },
        handleGoNewCard() {
            this.$router.push({name: "NewCard"});
        },
        handleGoAppointRegister() {
            this.$router.push({name: "AppointRegister"});
        },
        handleGoMyRegister(name) {
            let query = {};
            query["patId"] = this.dataSource.patId;
            query["patMedNo"] = this.dataSource.patMedNo;
            this.handleGoDetailPage(name, query);
        },
        handldeGoPersonalPay(name) {
            let query = {};
            query["patId"] = this.dataSource.patId;
            this.handleGoDetailPage(name, query);
        },
        handleGoPersonalCharge(name) {
            let query = {};
            query["patId"] = this.dataSource.patId;
            this.handleGoDetailPage(name, query);
        },
        handleGoOthersCharge(name) {
            let query = {};
            query["type"] = "Charge";
            this.handleGoDetailPage(name, query);
        },
        handleGoOthersPay(name) {
            let query = {};
            query["type"] = "Pay";
            this.handleGoDetailPage(name, query);
        },
        handleGoQueryReport(name) {
            let query = {};
            query["type"] = 0;
            this.handleGoDetailPage(name, query);
        },
        handleGoDetailPage(name, query) {
            let routeName = this.binded ? name : "BindCard";
            if (query) {
                this.$router.push({name: routeName, query});
            }
            else {
                this.$router.push({name: routeName});
            }
        },
        checkCookie() {
            // 初始化的时候清除缓存的token、systemType、init信息
            if (window.location.href.includes("systemType")) {
                this.globalMethods.delCookie("token");
                this.globalMethods.delCookie("systemType");
                this.globalMethods.delCookie("init");
            };
            let init = this.globalMethods.getCookie("init");
            let token = this.globalMethods.getCookie("token");
            if (init != "yes" && !token) {
                if (!this.globalMethods.getCookie("systemType")) {
                    let url = window.location.href;
                    let systemType = url.split("=")[1];
                    this.globalMethods.setCookie("systemType", systemType);
                }
                this.globalMethods.setCookie("init", "yes", "s8");
                this.globalMethods.login();
            }
        },
        setToken() {
            this.checkCookie();
            let owner = this;
            let token = this.globalMethods.getCookie("token");
            // 若cookie里没有token就去获取token
            if(!token) {
                let url = window.location.href.split("?")[1];
                let search = url.split("&");
                for (let i = 0; i < search.length; i++) {
                    let item = search[i];
                    if (item.includes("code")) {
                        var code = item.split("=")[1];
                        break;
                    }
                };
                if (code) {
                    this.$axios({
                        method:'post',
                        url: owner.$store.getters.getToken,
                        data: {
                            code,
                            systemType: owner.globalMethods.getCookie("systemType")
                        }
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            let data = response.data;
                            let token = data.Data;
                            owner.globalMethods.setCookie("token", token);
                            // 调用接口
                            owner.queryCardInfo();
                        }
                    })
                }
            }
            else {
                // 若cookie里面有token则直接调用接口
                owner.queryCardInfo();
            }
        },
        queryCardInfo() {
            let owner = this;
            // 把token设置到请求头里面
            owner.globalMethods.setRequestHeader();
            owner.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
            owner.$axios({
                    method: 'post',
                    url: owner.$store.getters.queryCardInfo,
                    data: {patId: ""}
                }).then ((response) => {
                    if (response.data.Code == 0) {
                        let dataArry = response.data.Data;
                        for (let i = 0; i < dataArry.length; i++) {
                            let item = dataArry[i];
                            if (item.isDefault == 0) {
                                owner.dataSource = item;
                                break;
                            }
                        }
                        owner.$store.commit("binded", true);
                        owner.$toast.clear();
                    }
                    else if (response.data.Code == -2) {
                        owner.globalMethods.delCookie("token");
                        window.location.reload();
                    }
                    else {
                        owner.$store.commit("binded", false);
                        owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: true
                        })
                    };
                })
        },
    }
};

</script>
<style scoped>
.more-service .left {
    width: 167.5px;
    height: 170px;
}
.more-service .right {
    width: 167.5px;
    height: 170px;
}
</style>



