<template>
    <div>
        <com-card-detail :type="0" :data="dataSource" :is-default="dataSource.isDefault" :btn-disabled="btnDisabled" @go-charge="handleGoPersonalPay" @delete-card="handleUnbind" @switch-change="handleSetDefault($event, dataSource.patId)" ref="cardDetail"></com-card-detail>
    </div>
</template>
<script>
import CCardDetail from "../../common/CCardDetail";
export default {
    name: "CardDetail",
    components: {
        [CCardDetail.name]: CCardDetail,
    },
    data() {
        return {
            btnDisabled: false,
            dataSource: {}
        };
    },
    mounted() {
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        let patId = this.patId;
        let owner = this;
        owner.globalMethods.setRequestHeader();
        this.$axios({
                    method:'post',
                    url: owner.$store.getters.queryCardInfoById,
                    data: {
                        code: patId,
                        type: 0
                    }
                }).then((response) => {
                    if (response.data.Code == 0) {
                        owner.dataSource = response.data.Data[0];
                        owner.$toast.clear();
                    }
                    else if (response.data.Code == -2) {
                        owner.$toast({
                            type: "fail",
                            message: "toke过期，即将重新登录",
                            forbidClick: true,
                            onOpened: () => {
                                owner.globalMethods.delCookie("token");
                                owner.$router.push("Outpatient");
                            }
                        });
                    }
                    else {
                        owner.toastFail(response.data.Msg);
                    }
                })
    },
    computed: {
        patId() {
            return this.$route.query.patId;
        }
    },
    methods: {
        handleGoPersonalPay() {
            let patId = this.patId;
            this.$router.push({name: "Charge", query: {patId}});
        },
        toastFail(msg) {
            this.$toast({
                type: "fail",
                message: msg,
                forbidClick: true
            })
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.$router.go(-1);
                }
            })
        },
        handleUnbind() {
            this.btnDisabled = true;
            let owner = this;
            let patId = this.patId;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                    method:'post',
                    url: owner.$store.getters.unBindCard,
                    data: {patId}
                }).then((response) => {
                    if (response.data.Code == 0) {
                        owner.toastSuccess("解绑成功");
                    }
                    else if (response.data.Code == -2) {
                        owner.$toast({
                            type: "fail",
                            message: "toke过期，即将重新登录",
                            forbidClick: true,
                            onOpened: () => {
                                owner.globalMethods.delCookie("token");
                                owner.$router.push("Outpatient");
                            }
                        });
                    }
                    else {
                        owner.btnDisabled = false;
                        owner.toastFail(response.data.Msg)
                    }
                })
        },
        handleSetDefault(isDefault, patId) {
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                        method:'post',
                        url: owner.$store.getters.updateCardInfo,
                        data: {
                            isDefault,
                            patId
                        }
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            console.log("设置默认卡成功");
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.$refs.cardDetail.toggleSwitch(true);
                            owner.$toast({
                                type: "fail",
                                message: response.data.Msg,
                                forbidClick: true
                            });
                        }
                    })
        },
    }
};

</script>
<style scoped>

</style>



