<template>
    <div>
        <com-card-detail type="1" :data="dataSource" :is-default="dataSource.isDefault" :btn-disabled="btnDisabled" @go-charge="handleGoChargeDeposit" @delete-card="handleUnbind" @switch-change="handleSetDefault($event, dataSource.inHosNo)" ref="inHosDetail"></com-card-detail>
    </div>
</template>
<script>
import CCardDetail from "../../common/CCardDetail";
export default {
    name: "CardDetail",
    components: {
        [CCardDetail.name]: CCardDetail,
    },
    data() {
        return {
            btnDisabled: false,
            dataSource: {}
        };
    },
    mounted() {
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        let inHosNo = this.$route.query.inHosNo;
        let owner = this;
        owner.globalMethods.setRequestHeader();
        this.$axios({
                    method:'post',
                    url: owner.$store.getters.queryPatientInfoByID,
                    data: {inHosNo}
                }).then((response) => {
                    if (response.data.Code == 0) {
                        console.log(response)
                        owner.dataSource = response.data.Data;
                        owner.$toast.clear();
                    }
                    else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                    }
                    else {
                        owner.toastFail(response.data.Msg);
                    }
                })
    },
    methods: {
        handleGoChargeDeposit() {
            let inHosNo = this.dataSource.inHosNo;
            this.$router.push({name: "ChargeDeposit", query: {inHosNo}});
        },
        toastFail(msg) {
            this.$toast({
                type: "fail",
                message: msg,
                forbidClick: true
            })
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.$router.go(-1);
                }
            })
        },
        handleUnbind() {
            this.btnDisabled = true;
            let owner = this;
            let inHosNo = this.$route.query.inHosNo;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                    method:'post',
                    url: owner.$store.getters.unbindPatientInfo,
                    data: {inHosNo}
                }).then((response) => {
                    if (response.data.Code == 0) {
                        owner.toastSuccess("解绑成功");
                    }
                    else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                    }
                    else {
                        owner.btnDisabled = false;
                        owner.toastFail(response.data.Msg)
                    }
                })
        },
        handleSetDefault(isDefault, inHosNo) {
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                        method:'post',
                        url: owner.$store.getters.updatePatientInfo,
                        data: {
                            isDefault,
                            inHosNo
                        }
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            console.log("设置默认住院号成功");
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.$refs.inHosDetail.toggleSwitch(true);
                            owner.$toast({
                                type: "fail",
                                message: response.data.Msg,
                                forbidClick: true
                            });
                        }
                    })
        },
    }
};

</script>
<style scoped>
.summary-bg {
    height: 210px;
    height: 120px;
    background: url("../../../assets/images/img/app/mzcz_bg.png") no-repeat;
    background-size: cover;
    position: relative;
}
.summary {
    background-color: #fff;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    top: 97px;
    width: 90%;
    height: 100px;
    border-radius: 10px;
    font-size: 13px;
}
.avatar {
    width: 50px;
    height: 50px;
    position: absolute;
    top: -28px;
    left: 50%;
    margin-left: -25px;
}
.summary .row1 {
    margin: 30px 0 10px 0;
}
.summary .row1 .name {
    font-size: 14px;
    color: black;
    font-weight: bold;
    margin-right: 7px;
}
.summary .row1 .sex {
    margin-right: 10px;
}
.summary .row2 .btn {
    color: blue;
}
.title {
    font-size: 14px;
    color: #999;
    text-align: left;
    padding: 7px;
}
.personal-info {
    margin-top: 30px;
}
.personal-info .collect {
    color: blue;
}
.btn-group {
    margin-top: 20px;
    color: #fff;
}
.btn-group .btn {
    width: 50%;
    font-size: 13px;
}
.cell-value .rmb {
    margin-right: 10px;
}
.cell-value .charge {
    color: blue;
}
</style>


