<template>
    <div>
        <div class="card-detail-wrap p-l-15 p-r-15 p-t-25">
            <div class="profile flex_dom flex_item_mid">
                <div class="avatar pr m-r-15">
                    <img class="pa abs-mid-X" src="../../assets/images/gentleman.png" v-if="dataInner.patSex == 1">
                    <img class="pa abs-mid-X" src="../../assets/images/lady.png" v-if="dataInner.patSex == 2">
                    <span class="patSex pa bgimg100" :class="classObj"></span>
                </div>
                <div class="info flex_column flex_item_between color-fff">
                    <div class="name tl font18 font-w6">{{dataInner.patName}}</div>
                    <div class="code font12">
                        <span>卡号：</span>
                        <span>{{dataInner.patMedNo}}</span>
                    </div>
                </div>
            </div>
            <div class="content m-t-25">
                <div class="panel p-l-15 p-r-15">
                    <div class="title tl">个人信息</div>
                    <div class="cell border-bottom flex_dom flex_item_between">
                        <span class="label">手机号</span>
                        <span class="value">{{dataInner.patPhone}}</span>
                    </div>
                    <div class="cell flex_dom flex_item_between">
                        <span class="label">身份证号</span>
                        <span class="value">{{dataInner.patIdNo}}</span>
                    </div>
                </div>
                <div class="panel p-l-15 p-r-15 m-t-10">
                    <div class="title tl">就诊卡信息</div>
                    <div class="cell border-bottom flex_dom flex_item_between">
                        <span class="label">余额</span>
                        <div class="value">
                            <span class="balance">{{dataInner.cardBalance}}</span>
                            <span class="unit">元</span>
                            <span class="charge m-l-15" @click="handleOnCharge">去充值</span>
                        </div>
                    </div>
                    <div class="cell flex_dom flex_item_between">
                        <span class="label">建卡日期</span>
                        <span class="value">{{dataInner.operateDate}}</span>
                    </div>
                </div>
                <div class="set-default flex_dom flex_item_between flex_item_mid m-t-10 p-l-15 p-r-15">
                    <span>设置为默认就诊卡</span>
                    <van-switch v-model="checked" size="15" @change="handleOnChange" />
                </div>
                <div class="delete m-t-20" @click="handleOnDeleteCard">删除就诊人</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "com-card-detail",
    components: {

    },
    data() {
        return {
            checked: this.isDefault
        };
    },
    props: {
        data: {
            type: Object,
            default() {
                return {
                    patName: "患者姓名",
                    patSex: "1",
                    patMedNo: "11111111111111",
                    patPhone: "13674848495",
                    patIdNo: "4113281993098765437",
                    cardBalance: "18.4",
                    operateDate: "2020-09-09 08:56:49",
                }
            }
        },
        isDefault: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        dataInner() {
            return this.data;
        },
        classObj() {
            return {
                gentleman: this.dataInner.patSex == 1,
                lady: this.dataInner.patSex == 2
            }
        }
    },
    methods: {
        handleOnChange() {
            this.$emit("switch-change", this.checked);
        },
        handleOnCharge() {
            this.$emit("go-charge");
        },
        handleOnDeleteCard() {
            this.$emit("delete-card");
        }
    }
};
</script>
<style scoped>
.card-detail-wrap {
    height: 100vh;
    background: url('../../assets/images/card_bg.png') no-repeat;
    background-size: 100%;
    background-color: #F3F5F9;
}
.profile .avatar {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    border: 1px solid #7BB6EC;
    background-color: #E3F4FC;
}
.profile .avatar img {
    width: 70%;
    height: 80%;
    bottom: 0;
}
.profile .avatar .patSex {
    width: 17px;
    height: 16px;
    bottom: 0;
    right: -2px;
}
.profile .avatar .patSex.gentleman {
    background: url('../../assets/images/man.png') no-repeat;
}
.profile .avatar .patSex.lady {
    background: url('../../assets/images/woman.png') no-repeat;
}
.profile .info {
    height: 45px;
}
.content .panel {
    background-color: #fff;
    border-radius: 10px;
}
.content .panel .title {
    height: 48px;
    line-height: 48px;
    color: #292C34;
    font-size: 18px;
    font-weight: bold;
}
.content .panel .cell {
    height: 46px;
    line-height: 46px;
}
.border-bottom {
    border-bottom: 0.5px solid #EEEEEE;
}
.content .panel .value {
    font-family: "Roboto-Regular";
}
.balance {
    font-size: 18px;
    color: #FF6257;
}
.charge {
    font-size: 13px;
    color: #0078FF;
}
.set-default {
    height: 45px;
    background-color: #fff;
    border-radius: 10px;
}
.delete {
    height: 43px;
    line-height: 43px;
    color: #FF6257;
    font-size: 16px;
    border-radius: 10px;
    background-color: #fff;
}
</style>


