<template>
    <div class="container">
        <div class="card">
            <div class="card-code">
                <span class="label">住院号：</span>
                <span class="code">{{dataSource.inHosNo}}</span>
            </div>
            <div class="card-name">
                <span class="label">{{dataSource.patName}}</span>
                <span class="code">{{dataSource.patIdNo}}</span>
            </div>
        </div>
        <van-cell-group class="cellphone">
            <van-cell title="手机号码" :value="dataSource.patPhone" title-class="cellphone-num-label" value-class="cellphone-num"/>
            <van-cell title="余额" :value="dataSource && Number(dataSource.deposit).toFixed(2) + '元'" title-class="balance-label" value-class="balance"/>
        </van-cell-group>
        <van-cell-group>
            <van-cell title="充值金额" title-style="text-align: left"/>
            <van-field
            v-model="amount"
            type="number"
            center
            clearable
            label="￥"
            label-width="30"
            placeholder="请输入充值金额"
            >
            <template #extra >
                <span class="unit">元</span>
            </template>
            </van-field>
        </van-cell-group>
        <van-button class="charge-btn" :disabled="btnDisabled" round type="info" @click="handleCharge">充值</van-button>
    </div>
</template>
<script>
export default {
    name: "ChargeDeposit",
    data() {
        return {
            inHosNo: "",
            amount: "",
            dataSource: "",
            btnDisabled: false
        };
    },
    mounted() {
        this.inHosNo = this.$route.query.inHosNo;
        let owner = this;
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        owner.globalMethods.setRequestHeader();
        this.$axios({
                method:'post',
                url: owner.$store.getters.queryPatientInfoByID,
                data: {
                    inHosNo: this.inHosNo,
                }
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    owner.dataSource = resData;
                    owner.$toast.clear();
                }
                else if (response.data.Code == -2) {
                    owner.$toast({
                        type: "fail",
                        message: "toke过期，即将重新登录",
                        forbidClick: true,
                        onOpened: () => {
                            owner.globalMethods.delCookie("token");
                            owner.$router.push("Outpatient");
                        }
                    });
                }
                else {
                    owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: true
                        })
                }
            });
    },
    methods: {
        handleCharge() {
            this.btnDisabled = true;
            if (!this.amount || this.amount == 0) {
                this.$toast({
                            type: "fail",
                            message: "充值金额必须大于零",
                            forbidClick: true
                        });
                        this.btnDisabled = false;
                        return;
            };
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                method:'post',
                url: owner.$store.getters.getJsApi,
                data: {
                    patId: owner.dataSource.patId,
                    body: "续交押金",
                    fee: owner.amount,
                    detail: "续交押金",
                    visitNo: owner.dataSource.inHosNo,
                    attach: "5-" + owner.dataSource.inHosNo + "-"
                }
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    let systemType = owner.globalMethods.getCookie("systemType");
                    if (systemType == "0A") {
                        owner.alipayCharge(resData);
                    }
                    else if (systemType == "0B") {
                        owner.wxCharge(resData);
                    }
                }
                else if (response.data.Code == -2) {
                    owner.$toast({
                        type: "fail",
                        message: "toke过期，即将重新登录",
                        forbidClick: true,
                        onOpened: () => {
                            owner.globalMethods.delCookie("token");
                            owner.$router.push("Outpatient");
                        }
                    });
                }
                else {
                    owner.btnDisabled = false;
                    owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: true
                        })
                }
            });
        },
        wxCharge(resData) {
            function onBridgeReady() {
                WeixinJSBridge.invoke(
                    'getBrandWCPayRequest',
                    {
                        "appId": resData.appId,//公众号名称，由商户传入     
                        "timeStamp": resData.timeStamp,//时间戳，自1970年以来的秒数
                        "nonceStr": resData.nonceStr, //随机串     
                        "package": resData.package,     
                        "signType": resData.signType,//微信签名方式：     
                        "paySign": resData.paySign //微信签名 
                    },
                    function(res) {
                        if (res.err_msg == "get_brand_wcpay_request:ok") {
                            //关闭微信浏览器  安卓系统
                            document.addEventListener('WeixinJSBridgeReady', function(){ WeixinJSBridge.call('closeWindow'); }, false);
                            //关闭微信浏览器   ios系统
                            WeixinJSBridge.call('closeWindow');
                        }
                        else {
                            owner.$set(owner.btnDisabled, index, false);
                        }
                    }
                );
            };
            // 判断是否是微信环境
            if (typeof WeixinJSBridge == "undefined") {
                owner.$set(owner.btnDisabled, index, false);
                if (document.addEventListener) {
                    document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
                }
                else if (document.attachEvent) {
                    document.attachEvent('WeixinJSBridgeReady', onBridgeReady); 
                    document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
                }
            }
            else {
                onBridgeReady();
            }
        },
        alipayCharge(resData) {
            function ready(callback) {
                // 如果jsbridge已经注入则直接调用
                if (window.AlipayJSBridge) {
                    callback && callback();
                } else {
                    // 如果没有注入则监听注入的事件
                    document.addEventListener('AlipayJSBridgeReady', callback, false);
                }
            };
            ready(function(){
                AlipayJSBridge.call("tradePay", {
                    orderStr: resData.strPara
                }, function(result) {
                    AlipayJSBridge.call('closeWebview');
                });
            });
        }
    }
};

</script>
<style scoped>
.container {
    height: 100vh;
    background-color: #F3F5F9;
    padding: 10px 15px;
}
.container >>> .van-cell::after {
    right: 16px;
}
.card {
    width: 95%;
    background: linear-gradient(#3853e8, #2cb8f7);
    height: 100px;
    border-radius: 10px;
    margin: 20px auto;
    box-sizing: border-box;
    color: #fff;
    padding: 10px;
    font-size: 14px;
}
.card-code {
    text-align: left;
    margin-bottom: 30px;
}
.card-name {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.card-name .label{
    font-size: 18px;
}
.cellphone {
    margin-bottom: 20px;
    border-radius: 10px;
    overflow: hidden;
}
.van-cell-group {
    border-radius: 10px;
    overflow: hidden;
}
.cellphone-num-label {
    text-align: left;
    color: #9e9e9e;
}
.cellphone-num {
    color: black;
}
.balance {
    color: black;
}
.balance-label {
    text-align: left;
    color: #9e9e9e;
}
.unit {
    margin-left: 10px;
}
.charge-btn {
    width: 80%;
    height: 30px;
    line-height: 30px;
    margin: 40px auto;
}
</style>



