<template>
    <div class="main-bg">
        <van-cell class="radius-10" title-style="text-align: left;" title="就诊人" :value="patName" />
        <van-row class="date-btn" type="flex" gutter="10">
            <van-col span="6" offset="6" @click="handleQueryReport(-7)"><span :class="{active: btnIndex == -7}">七天</span></van-col>
            <van-col span="6" @click="handleQueryReport(-30)"><span :class="{active: btnIndex == -30}">一个月</span></van-col>
            <van-col span="6" @click="handleQueryReport(-90)"><span :class="{active: btnIndex == -90}">三个月</span></van-col>
        </van-row>
        <van-tabs 
            class="report-type"
            v-model="active"
            title-inactive-color="#818594" 
            title-active-color="#292C34"
        >
            <van-tab title="检验报告">
                <template #title>
                    <span class="custom-tab-title">检验报告</span>
                </template>
                <div class="card-item radius-10" @click="handleGoReportDetail(item.applyNo, 'inspe')" v-for="(item, index) in dataSourceInspe" :key="'report1' + index">
                    <van-cell-group>
                        <van-cell title-style="text-align: left; font-weight: bold; color: #292c34;" :title="item.itemName" :value="item.applyDate" />
                        <van-cell title-style="text-align: left;" :title="item.diagnosis" center>
                            <template #right-icon>
                                <van-icon name="arrow"/>
                            </template>
                        </van-cell>
                    </van-cell-group>
                </div>
                <van-empty description="暂无数据" v-if="dataSourceInspe.length == 0"/>
            </van-tab>
            <van-tab title="检查报告">
                <div class="card-item radius-10" @click="handleGoReportDetail(item.applyNo, 'check')" v-for="(item, index) in dataSourceCheck" :key="'report2' + index">
                    <van-cell-group>
                        <van-cell title-style="text-align: left; font-weight: bold; color: #292c34;" :title="item.itemName" :value="item.applyDate" />
                        <van-cell title-style="text-align: left;" :title="item.diagnostic" center>
                            <template #right-icon>
                                <van-icon name="arrow"/>
                            </template>
                        </van-cell>
                    </van-cell-group>
                </div>
                <van-empty description="暂无数据" v-if="dataSourceCheck.length == 0"/>
            </van-tab>
        </van-tabs>
    </div>
</template>
<script>
export default {
    name: "QueryReport",
    data() {
        return {
            active: 0,
            startDate: "",
            endDate: "",
            dataSourceInspe: "",
            dataSourceCheck: "",
            patName: "",
            btnIndex: ""
        };
    },
    mounted() {
        this.handleQueryReport(-7);
    },
    methods: {
        handleGoReportDetail(val, type) {
            if (type == "check") {
                var data, dataSourceCheck = this.dataSourceCheck;
                for (let i = 0; i < dataSourceCheck.length; i ++) {
                    let item = dataSourceCheck[i];
                    if (item.applyNo == val) {
                        data = item;
                        break;
                    }
                }
            };
            this.$router.push({name: "ReportDetail", params: {data}, query: {applyNo: val, type}});
        },
        handleQueryReport(val) {
            this.btnIndex = val;
            this.startDate = this.getDate(val);
            this.endDate = this.getDate(0);
            let owner = this;
            this.$toast.loading({message: '加载中...', forbidClick: false, duration: 0});
            owner.globalMethods.setRequestHeader();
            this.$axios({
                    method:'post',
                    url: owner.$store.getters.queryReportInspe,
                    data: {
                        type: owner.$route.query.type,
                        startDate: owner.startDate,
                        endDate: owner.endDate
                    }
                }).then ((response) => {
                    if (response.data.Code == 0) {
                        owner.dataSourceInspe = response.data.Data;
                        owner.patName = owner.dataSourceInspe[0].patName;
                        owner.$toast.clear();
                    }
                    else if (response.data.Code == -2) {
                        owner.$toast({
                            type: "fail",
                            message: "toke过期，即将重新登录",
                            forbidClick: true,
                            onOpened: () => {
                                owner.globalMethods.delCookie("token");
                                owner.$router.push("Outpatient");
                            }
                        });
                    }
                    else {
                        owner.dataSourceInspe = "";
                        owner.$toast({
                                type: "fail",
                                message: response.data.Msg,
                                forbidClick: false
                            })
                    }
                });
            this.$axios({
                method:'post',
                url: owner.$store.getters.queryReportCheck,
                data: {
                    type: owner.$route.query.type,
                    startDate: owner.startDate,
                    endDate: owner.endDate
                }
            }).then ((response) => {
                if (response.data.Code == 0) {
                    owner.dataSourceCheck = response.data.Data;
                    owner.$toast.clear();
                }
                else {
                    owner.dataSourceCheck = "";
                }
            });
        },
        getDate(val) {
            let date = this.globalMethods.getDate(val);
            return date.year + "-" + date.month + "-" + date.day;
        }
    }
};

</script>
<style scoped>
.date-btn {
    margin: 10px 0;
}
.date-btn .van-col>span{
    background-color: #0078ff;
    display: block;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: #fff;
    font-size: 14px;
}
.date-btn .van-col>span.active {
    box-shadow: 0px 0px 8px #3c404c;
    transform:scale(1.1, 1.1);
}
.card-item {
    margin: 20px 0;
}
.report-type {
    border-radius: 10px;
    overflow: hidden;
}
.report-type >>> .van-tabs__line {
    background-color: #0078FF;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    width: 30px!important;
    bottom: 20px;
}
.report-type >>> .van-tab--active {
    font-weight: bold;
}
.custom-tab-title {
    display: block;
    width: 172px;
    border-right: .5px solid #CDCDCD;
}
</style>



