<template>
    <div class="container">
        <van-row class="date-btn" type="flex" gutter="10">
            <van-col span="6" offset="6" @click="queryPayRecord(-7)"><span :class="{active: btnIndex == -7}">七天</span></van-col>
            <van-col span="6" @click="queryPayRecord(-30)"><span :class="{active: btnIndex == -30}">一个月</span></van-col>
            <van-col span="6" @click="queryPayRecord(-90)"><span :class="{active: btnIndex == -90}">三个月</span></van-col>
        </van-row>
        <div>
            <van-cell-group class="card-item" v-for="(item, index) in dataSource" :key="'list' + index">
                <van-cell title-style="text-align: left;" title="就诊卡号" :value="item.patMedNo" />
                <van-cell title-style="text-align: left;" title="充值金额" :value="item.chargeAmount + '元'"/>
                <van-cell title-style="text-align: left;" title="充值方式" :value="item.payType == '0A' ? '支付宝' : '微信'"/>
                <van-cell title-style="text-align: left;" title="充值时间" :value="item.chargeDate"/>
            </van-cell-group>
            <van-empty description="暂无数据" v-if="dataSource.length == 0"/>
        </div>
    </div>
</template>
<script>
export default {
    name: "PayRecord",
    data() {
        return {
            dataSource: "",
            startDate: "",
            endDate: "",
            btnIndex: ""
        };
    },
    mounted() {
        this.queryPayRecord(-7)
    },
    methods: {
        queryPayRecord(val) {
            this.btnIndex = val;
            this.getDate(val);
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                        method:'post',
                        url: owner.$store.getters.queryPayRecord,
                        data: {
                            startDate: this.startDate,
                            endDate: this.endDate
                        }
                    }).then ((response) => {
                        if (response.data.Code == 0) {
                            owner.dataSource = response.data.Data;
                            owner.$toast.clear();
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.dataSource = "";
                            owner.$toast({
                                    type: "fail",
                                    message: response.data.Msg,
                                    forbidClick: false
                                })
                        }
                    });
        },
        getDate(val = 0) {
            let startDate = this.globalMethods.getDate(val);
            let endDate = this.globalMethods.getDate(0);
            this.startDate = startDate.year + "-" + startDate.month + "-" + startDate.day;
            this.endDate = endDate.year + "-" + endDate.month + "-" + endDate.day;
        }
    }
};

</script>
<style scoped>
.container {
    min-height: 100vh;
    background-color: #F3F5F9;
    padding: 10px 15px;
}
.date-btn {
    margin: 10px 0;
}
.date-btn .van-col>span{
    background-color: #0078ff;
    display: block;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: #fff;
    font-size: 14px;
}
.date-btn .van-col>span.active {
    box-shadow: 0px 0px 8px #3c404c;
    transform:scale(1.1, 1.1);
}
.card-item {
    margin-bottom: 15px;
    border-radius: 10px;
    overflow: hidden;
}
</style>



