// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store/index';
import axios from 'axios';
import methods from './methods/index';
import vant from './vant/index';
import './styles/global.css';
import './styles/common.css';
import 'amfe-flexible/index.js'
Vue.config.productionTip = false;
Vue.prototype.$axios = axios;

// 解决跨域
// axios.defaults.baseURL = '/api';

// 引入mock数据，关闭则注释该行
require('./mock');

// 初始化项目中依赖的vant组件
for (let i = 0; i < vant.vantComs.length; i++) {
    Vue.use(vant.vantComs[i]);
};

// 为vue添加全局方法
Vue.prototype.globalMethods = methods;
// 从cookie里读取token设置到请求头里
Vue.prototype.globalMethods.setRequestHeader = function() {
    axios.defaults.headers.common["auth"] = methods.getCookie("token");
};
// 用户登录返回重定向url
Vue.prototype.globalMethods.login = function() {
    axios({
        method: "post",
        url: store.getters.getAuthUrl,
        data: {
            redirect_url: "#/outpatient",
            systemType: methods.getCookie("systemType")
        }
    }).then((response) => {
        if (response.data.Code == 0) {
            let data = response.data.Data;
            window.location = data;
        }
    })
};

// show title by current router
router.beforeEach((to, from, next) => {
    if (to.meta.title) {
        document.title = to.meta.title;
    };
    next();
});
/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    axios,
    components: { App },
    template: '<App/>'
});



