<template>
    <div class="switch-box" :class="classObj" @click="change">
        <span class="switch-btn">{{text}}</span>
        <input class="switch-input" type="checkbox" v-model="isChecked">
    </div>
</template>
<script>
export default {
  name: "base-switch",
  data() {
    return {
        isChecked: this.checked
    };
  },
  model: {
    prop: 'checked',
    event: 'change'
  },
  props: {
    checked: Boolean,
    textOff: String,
    textOn: String
  },
  computed: {
      text() {
          return this.isChecked ? (this.textOn || '儿童') : (this.textOff || '成人');
      },
      classObj() {
          return {
              'to-left': !this.isChecked,
              'to-right': this.isChecked
          }
      }
  },
  methods: {
      change() {
          this.isChecked = !this.isChecked;
          this.$emit('change', this.isChecked);
      }
  }
};
</script>
<style scoped>
.switch-input {
    display: none;
}
.switch-box {
    display: inline-block;
    width: 100px;
    height: 30px;
    line-height: 30px;
    background-color: blue;
    border-radius: 30px;
    position: relative;
    transition: all .5s;
    -moz-transition: all .5s; /* Firefox 4 */
    -webkit-transition: all .5s; /* Safari and Chrome */
    -o-transition: all .5s; /* Opera */
}
.switch-btn {
    width: 50px;
    height: 30px;
    color: #fff;
    text-align: center;
    line-height: 30px;
    background-color: green;
    border-radius: 30px;
    position: absolute;
    left: 0px;
    top: 0;
    transition: all .5s;
    -moz-transition: all .5s; /* Firefox 4 */
    -webkit-transition: all .5s; /* Safari and Chrome */
    -o-transition: all .5s; /* Opera */
}
.to-left {
    background-color:gray;
}
.to-right {
    background-color: green;
}
.to-left .switch-btn{
    left: 0px;
    background-color: black;
}
.to-right .switch-btn{
    left: 50px;
    background-color: blue;
}
</style>



