<template>
    <div class="main-bg">
        <van-form @submit="handleSubmit" :show-error-message="false">
            <div class="radius-10">
                <van-field
                v-model="patName"
                name="patName"
                placeholder="请输入姓名"
                label="姓名"
                label-width="120"
                :rules="[{ required: true, message: '请输入姓名' }]"
                clearable
                ></van-field>
                <van-field
                name="patIdNo"
                v-model="patIdNo"
                type="number"
                label="身份证号"
                placeholder="请输入身份证号"
                label-width="120"
                :rules="[{ required: false, message: '请输入身份证号' }]"
                maxlength="18"
                clearable
                />
                <van-field
                name="patPhone"
                v-model="patPhone"
                type="number"
                label="手机号"
                placeholder="请输入手机号"
                label-width="120"
                :rules="[{ required: false, message: '请输入手机号' }]"
                maxlength="11"
                clearable
                />
            </div>
            <div class="tips">
                <span class="strong">温馨提示：</span><span>请输入患者相关信息，并再准确核对充值信息后，再执行充值操作！</span>
            </div>
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit" :disabled="submitDisabled" :loading="submitLoading">确定</van-button>
            </div>
            <div class="bottom-tips"><span>知道就诊卡号 </span><span class="strong" @click="handleGoQueryOthersCardByNum">去充值</span></div>
        </van-form>
      <van-popup class="select-card-modal" round v-model="showPopup" @close="handleClosePopup">
        <div class="popup">
            <div class="title">该用户有多张就诊卡，请选择要绑定的就诊卡</div>
            <div class="card" v-for="(item, index) in cardList" :key="'card' + index" @click="handleGoOthersChargeOrPay(item.patId)">
                <div class="wrap">
                    <div class="row">
                        <span class="name">{{item.patName}}</span>
                    </div>
                    <div class="row card-code">
                        <span>卡号 :</span>
                        <span>{{item.patMedNo}}</span>
                    </div>
                </div>
            </div>
        </div>
    </van-popup>
    </div>
</template>
<script>
export default {
    name: "QueryOthersCardByName",
    data() {
        return {
            type: "",
            patName: "",
            patIdNo: "",
            patPhone: "",
            cardList: "",
            submitLoading: false,
            submitDisabled: false,
            showPopup: false
        };
    },
    mounted() {
        this.type = this.$route.query.type;
    },
    methods: {
        handleSubmit(values) {
            //valid the form of patIdNo and patPhone
            if (!this.validForm(values)) {
                return;
            };
            this.submitLoading = true;
            this.submitDisabled = true;
            let owner = this;
            let data = {};
            data["patName"] = values.patName;
            data["patIdNo"] = values.patIdNo;
            data["patPhone"] = values.patPhone;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                        method:'post',
                        url: owner.$store.getters.queryCardList,
                        data
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            let resData = response.data.Data;
                            if (resData.length == 1) {
                                owner.handleGoOthersChargeOrPay(resData[0].patId);
                            }
                            else {
                                owner.cardList = resData;
                                owner.showPopup = true;
                            }
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.$toast({
                                type: "fail",
                                message: response.data.Msg,
                                forbidClick: true,
                                onOpened: () => {
                                    owner.submitDisabled = false;
                                    owner.submitLoading = false;
                                }
                            })
                        }
                    })
        },
        handleGoQueryOthersCardByNum() {
            let type = this.type;
            this.$router.replace({name: "QueryOthersCardByNum", query: {type}});
        },
        handleClosePopup() {
            this.submitLoading = false;
            this.submitDisabled = false;
        },
        handleGoOthersChargeOrPay(val) {
          let patId = val;
          let name = this.type;
          this.$router.replace({name, query: {patId}});
        },
        validForm(values) {
            if (!values.patIdNo && !values.patPhone) {
                this.$toast({
                    type: "fail",
                    message: "身份证号和手机号不能同时为空",
                    forbidClick: true
                });
                return false;
            };
            if (values.patIdNo) {
                if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                    this.$toast({
                        type: "fail",
                        message: "身份证号格式错误",
                        forbidClick: true
                    });
                    return false;
                }
                else {
                    return true;
                }
            };
            if(!/^1[3456789]\d{9}$/.test(values.patPhone)) {
                this.$toast({
                    type: "fail",
                    message: "手机号格式错误",
                    forbidClick: true
                });
                return false;
            };
            return true;
        }
    }
};
</script>
<style scoped>
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: #0078ff;
}
.bottom-tips {
    font-size: 12px;
    text-align: right;
    padding: 10px;
    margin: 10px;
    height: 20px;
    line-height: 20px;
}
.select-card-modal {
    z-index: 2002;
    width: 90%;
    min-height: 300px;
}
.popup {
    max-height: 400px;
    overflow: scroll;
    padding-bottom: 10px;
}
.popup .title {
    text-align: left;
    margin: 10px;
    font-size: 15px;
    color: #716f6f;
}
.popup .card {
    width: 97%;
    background-color: #1830b4;
    border-radius: 15px;
    padding: 15px;
    margin: 5px auto 0 auto;
    box-sizing: border-box;
}
.popup .card .wrap {
    height: 100%;
}
.popup .wrap .row {
    text-align: left;
    color: #fff;
    margin-bottom: 10px;
}
.popup .wrap .name {
    margin-right: 20px;
}
.popup .wrap .tag {
    background-color: #0c41d6;
    font-size: 12px;
    display: inline-block;
    padding: 2px 4px;
    border-radius: 2px;
    margin-right: 4px;
}
.popup .wrap .card-code {
    font-size: 14px;
    margin-bottom: 0px;
}
.popup .default-card {
    float: right;
}
</style>



