<template>
  <div class="main-bg">
    <van-form @submit="handleSubmit" :show-error-message="false">
        <div class="radius-10">
            <!-- 姓名 -->
            <van-field
                name="patName"
                v-model="patName"
                label="姓名"
                label-width="120"
                placeholder="请输入患者姓名"
                :rules="[{ required: true, message: '请输入患者姓名' }]"
                clearable
            />
            <!-- 身份证号码 -->
            <van-field
                name="patIdNo"
                v-model="patIdNo"
                label="身份证号"
                placeholder="请输入患者身份证号"
                label-width="120"
                maxlength="18"
                :rules="[{ required: false, message: '请输入患者身份证号' }]"
                clearable
            />
            <!-- 入院登记证编号 -->
            <van-field
                name="inHosCerNo"
                v-model="inHosCerNo"
                label="入院登记证编号"
                placeholder="请输入入院登记证编号"
                label-width="120"
                maxlength="18"
                :rules="[{ required: false, message: '请输入入院登记证编号' }]"
                clearable
            />
        </div>
      <!-- 提示 -->
      <div class="tips">
        <span class="strong">温馨提示：</span><span class="normal">请输入患者相关信息，并准确核对后再进行下一步操作！</span>
      </div>
      <div class="confirm-btn">
        <van-button round block type="info" :loading="submitLoading" loading-text="请稍后" :disabled="submitDisabled" native-type="submit">查询</van-button>
      </div>
    </van-form>
  </div>
</template>
<script>
export default {
    name: "QueryInhosCertificate",
    data() {
        return {
            inHosCerNo: "",
            patName: "",
            patIdNo: "",
            submitLoading: false,
            submitDisabled: false,
        };
    },
    methods: {
        handleSubmit(values) {
            //valid the form of patIdNo
            if (!this.validForm(values)) {
                return;
            };
            this.submitLoading = true;
            this.submitDisabled = true;
            let owner = this;
            let data = {};
            data["inHosCerNo"] = values.inHosCerNo;
            data["patName"] = values.patName;
            data["patIdNo"] = values.patIdNo;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                        method:'post',
                        url: owner.$store.getters.queryInHospitalCertificate,
                        data
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            let data = response.data.Data;
                            owner.$router.replace({name: "InHosRegisterInfo", params: {data}})
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.toastFail(response.data.Msg);
                        }
                    })
        },
        validForm(values) {
            if (!values.patIdNo && !values.inHosCerNo) {
                this.$toast({
                    type: "fail",
                    message: "身份证号和入院证编号不能同时为空",
                    forbidClick: true
                });
                return false;
            };
            if (values.patIdNo) {
                if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                    this.$toast({
                        type: "fail",
                        message: "身份证号格式错误",
                        forbidClick: true
                    });
                    return false;
                }
                else {
                    return true;
                }
            };
            return true;
        },
        toastFail(msg) {
            this.$toast({
                type: "fail",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.submitDisabled = false;
                    this.submitLoading = false;
                }
            })
        }
    }
};
</script>
<style scoped>
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: #0078ff;
}
.confirm-btn {
    margin: 16px;
}
.bottom-tips {
    font-size: 12px;
    text-align: right;
    padding: 10px;
    margin: 10px;
    height: 20px;
    line-height: 20px;
}
</style>



