<template>
    <div class="container">
        <van-row class="doctor-summary">
            <van-col class="pic" span="6">
                <van-image width="70" height="70" round :src="dataSource.doctorAvatar" />
            </van-col>
            <van-col class="des" span="18">
                <div><span class="name strong">{{dataSource.doctorName}}</span>{{dataSource.doctorTitle}}</div>
                <div>所属科室： <span class="strong">{{dataSource.deptName}}</span></div>
            </van-col>
        </van-row>
        <van-tabs 
            class="detail-type"
            v-model="active"
            :swipeable="true"
            title-inactive-color="#818594" 
            title-active-color="#292C34"
          >
            <van-tab title="排班信息">
                <template #title>
                    <span class="custom-tab-title">排班信息</span>
                </template>
                <van-row class="schedule-title">
                    <van-col span="6">
                        <van-icon size="20" :name="require('../../../assets/images/img/riqi.png')" />
                        <span>日期</span>
                    </van-col>
                    <van-col span="9">
                        <van-icon size="20" :name="require('../../../assets/images/img/shangwu.png')" />
                        <span>上午</span>
                    </van-col>
                    <van-col span="9">
                        <van-icon size="20" :name="require('../../../assets/images/img/xiawu.png')" />
                        <span>下午</span>
                    </van-col>
                </van-row>

                <van-row class="schedule-content active" v-for="(item, index) in dataSource.details" :key="'detail' + index">
                    <van-col class="date" span="6">
                        {{item.date.slice(5) + " " + item.week}}
                    </van-col>
                    <van-col class="am" span="9" @click="handleGoRegisterDetail(item.statusAM, item.date, 0)">
                        <span v-if="item.statusAM == 0">预约 ({{item.totalNumAM - item.availableNumAM}}/{{item.totalNumAM}})</span>
                        <span class="active1-item" v-else-if="item.statusAM == 1">约满 ({{item.totalNumAM}}/{{item.totalNumAM}})</span>
                        <span class="active2-item" v-else>未坐诊</span>
                    </van-col>
                    <van-col class="pm" span="9"  @click="handleGoRegisterDetail(item.statusPM, item.date, 1)">
                        <span v-if="item.statusPM == 0">预约 ({{item.totalNumPM - item.availableNumPM}}/{{item.totalNumPM}})</span>
                        <span class="active1-item" v-else-if="item.statusPM == 1">约满 ({{item.totalNumPM}}/{{item.totalNumPM}})</span>
                        <span class="active2-item" v-else>未坐诊</span>
                    </van-col>
                </van-row>
            </van-tab>
            <van-tab title="医生介绍">
                <div class="doctor-introduce">
                    <div class="row1">
                        <van-icon size="20" :name="require('../../../assets/images/img/chuzhen.png')" />
                        <span>出诊信息</span>
                    </div>
                    <div class="row2">
                        <van-icon size="20" :name="require('../../../assets/images/img/shanchang.png')" />
                        <span>简介</span>
                    </div>
                    <div class="row3">{{dataSource.doctorDes}}</div>
                </div>
                
            </van-tab>
        </van-tabs>
    </div>
</template>
<script>
export default {
    name: "DoctorDetail",
    data() {
        return {
            active: 0,
            dataSource: ""
        };
    },
    mounted() {
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        let owner = this;
        let querys = this.$route.query;
        let deptNo = querys.deptNo;
        let doctorNo = querys.doctorNo;
        owner.globalMethods.setRequestHeader();
        this.$axios({
                        method:'post',
                        url: owner.$store.getters.queryDoctorDetail,
                        data: {deptNo, doctorNo}
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            owner.dataSource = response.data.Data;
                            owner.$toast.clear();
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.$toast({
                                    type: "fail",
                                    message: response.data.Msg,
                                    forbidClick: true
                                })
                        }
                    })
    },
    methods: {
        handleGoRegisterDetail(status, date, timeFlag) {
            // check if binded card
            if (!this.$store.state.binded) {
                this.$router.push({name: "BindCard"});
                return;
            };
            let data = {};
            data["deptName"] = this.dataSource.deptName;
            data["deptNo"] = this.dataSource.deptNo;
            data["doctorName"] = this.dataSource.doctorName;
            data["doctorNo"] = this.dataSource.doctorNo;
            data["totalFee"] = this.dataSource.totalFee + "元";
            data["appointDate"] = date;
            data["timeFlag"] = timeFlag;
            if (status == 0) {
                this.$router.push({name: "RegisterDetail", params: {data}});
            }
        }
    }
};

</script>
<style scoped>
.container {
    min-height: 100vh;
    background-color: #F3F5F9;
    padding: 10px 15px;
}
.doctor-summary {
    background-color: #fff;
    padding: 0px 10px;
    height: 100px;
    margin-bottom: 10px;
    border-radius: 10px;
    overflow: hidden;
}
.doctor-summary .van-col {
    height: 100%;
    padding: 15px 0;
}
.doctor-summary .pic {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.doctor-summary .des {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: left;
    font-size: 14px;
    color: #88929d;
    padding-left: 10px;
}
.doctor-summary .des .name {
    margin-right: 20px;
}
.doctor-summary .des .strong{
    color: black;
}
.schedule-title {
    height: 40px;
}
.schedule-title .van-col {
    display: flex;
    justify-content:center;
    align-items: center;
    height: 100%;
    color: #88929d;
}
.schedule-title .van-col>span {
    font-size: 13px;
}
.schedule-content {
    height: 40px;
    margin-top: 10px;
}
.schedule-content .van-col {
    display: flex;
    justify-content:center;
    align-items: center;
    height: 100%;
    color: #88929d;
    font-size: 12px;
}
.schedule-content .van-col>span {
    height: 40px;
    line-height: 40px;
    width: 110px;
    text-align: center;
    color: #0078FF;
    background-color: #fff;
}
.schedule-content.active .am>span,
.schedule-content.active .pm>span {
    color: #fff;
    background-color: #0078FF;
    box-shadow: 0px 0px 5px #0aaaf3;
}
.schedule-content.active .date {
    color: #0078FF;
}
.schedule-content span.active1-item {
    background-color: #d6d9da !important;
    color: #58626c !important;
    box-shadow: none !important;
}
.schedule-content span.active2-item {
    background: #d6d9da url("../../../assets/images/img/tiaowen_bg.png") no-repeat!important;
    color: #58626c !important;
    box-shadow: none !important;
}
.doctor-introduce {
    padding: 10px;
}
.doctor-introduce>div {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}
.doctor-introduce .van-icon{
    margin-right: 10px;
}
.doctor-introduce .row2>span{
    font-size: 13px;
    color: #88929d;
}
.doctor-introduce .row3 {
    text-align: left;
    color: black;
    font-size: 14px;
    margin-top: 20px;
    line-height: 20px;
}

.detail-type {
    border-radius: 10px;
    overflow: hidden;
}
.detail-type>>> .van-tabs__line {
    background-color: #0078FF;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    width: 30px!important;
    bottom: 20px;
}
.detail-type >>> .van-tab--active {
    font-weight: bold;
}
.custom-tab-title {
    display: block;
    width: 172px;
    border-right: .5px solid #CDCDCD;
}
</style>



