<template>
    <div class="charge main-bg">
            <van-field
                class="radius-10"
                type="number"
                v-model="inHosNo"
                placeholder="请输入住院号"
                center
                clearable
            />
            <div class="tips">
                <span class="strong">温馨提示：</span><span>请输入患者相关信息，并再准确核对充值信息后，再执行充值操作！</span>
            </div>
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit" @click="handleGoCharge">确定</van-button>
            </div>
    </div>
</template>

<script>
export default {
    name: "QueryOthersInHosNo",
    data() {
        return {
            inHosNo: ""
        };
    },
    methods: {
        handleGoCharge() {
            if (this.inHosNo.length == 0) {
                this.$toast({
                    type: "fail",
                    message: "请输入住院号",
                    forbidClick: true
                });
                return;
            }
            this.$router.push({name: "ChargeDeposit", query: {inHosNo: this.inHosNo}});
        }
    }
};

</script>
<style scoped>
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: blue;
}

</style>



