<template>
    <div class="container">
        <van-notice-bar mode="closeable" background="#0ab4a4" color="#fff" :wrapable="true">
            <template #default>
                <div class="notice-bar-text">您可预约明天至未来一周内所有坐诊的医生，预约完成后请于预约时间之前数小时到医院取号排队候诊。</div>
            </template>
        </van-notice-bar>
        <van-collapse v-model="activeNames" accordion>
            <van-collapse-item v-for="(item, index) in dataSource" :key="'major' + index">
                <template #value>
                    <div class="collapse-item-val">
                        <van-icon :name="item.deptImg" size="20"/>
                        <span>{{item.deptProName}}</span>
                    </div>
                </template>
                <template #default>
                        <van-cell class="collapse-item-val-sub" v-for="(item, index) in item.deptList" :key="'dep' + index" @click="handleGoDoctorList(item.deptNo)">
                            <template #title>
                                <div class="collapse-item-val-sub1">
                                    <van-icon :name="require('../../../assets/images/img/wapManager/dot.png')" size="20"/>
                                    <span>{{item.deptName}}</span>
                                </div>
                            </template>
                            <template #extra>
                                <div class="collapse-item-val-sub2">
                                    <van-icon size="12" :name="require('../../../assets/images/img/arrow_right.png')"/>
                                </div>
                            </template>
                        </van-cell>
                </template>
            </van-collapse-item>
        </van-collapse>
    </div>
</template>
<script>
export default {
    name: "AppointRegister",
    data() {
        return {
            activeNames: [],
            dataSource: ""
        };
    },
    mounted() {
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        let owner = this;
        owner.globalMethods.setRequestHeader();
        this.$axios({
                        method:'post',
                        url: owner.$store.getters.queryDeptInfo,
                        data: {type: 0}
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            console.log(response)
                            owner.dataSource = response.data.Data;
                            owner.$toast.clear();
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.$toast({
                                type: "fail",
                                message: response.data.Msg,
                                forbidClick: true
                            })
                        }
                    })
    },
    methods: {
        handleGoDoctorList(deptNo) {
            this.$router.push({name: "DoctorList", query: {deptNo}});
        }
    }
};

</script>
<style scoped>
.container {
    min-height: 100vh;
    background-color: #F3F5F9;
    padding: 10px 15px;
}
.van-notice-bar {
    margin-bottom: 20px;
    border-radius: 5px;
}
.notice-bar-text {
    font-size: 12px;
    text-align: left;
    line-height: 17px;
}
.collapse-item-val {
    display: flex;
    align-items: center;
}
.collapse-item-val .van-icon {
    margin-right: 15px;
}
.collapse-item-val-sub {
    padding: 0;
    background-color: #ebebf1;
    padding: 12px 0;
}
.collapse-item-val-sub1{
    display: flex;
    align-items: center;
    padding-left: 40px;
    box-sizing: border-box;
    height: 100%;
}
.collapse-item-val-sub2 {
    padding-right: 4px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
}
.van-collapse {
    border-radius: 10px;
    overflow: hidden;
}
.van-collapse-item>>>.van-collapse-item__content {
    padding: 0 16px;
    background-color: #ebebf1;
}
</style>



