const getters = {
    //api
    ///////////////////////////////////登录//////////////////////////////////////////////////
    getAuthUrl: state => state.url + state.api.getAuthUrl,// 获取authUrl
    getToken: state => state.url + state.api.getToken,// 获取token
    ///////////////////////////////////门诊服务模块///////////////////////////////////////////
    queryCardList: state => state.url + state.api.queryCardList,
    bindCard: state => state.url + state.api.bindCard,
    createCard: state => state.url + state.api.createCard,
    queryCardInfo: state => state.url + state.api.queryCardInfo,
    unBindCard: state => state.url + state.api.unBindCard,
    updateCardInfo: state => state.url + state.api.updateCardInfo,
    queryDeptInfo: state => state.url + state.api.queryDeptInfo,
    getDoctorList: state => state.url + state.api.getDoctorList,
    queryDoctorDetail: state => state.url + state.api.queryDoctorDetail,
    appointRegisterOrCancel: state => state.url + state.api.appointRegisterOrCancel,
    queryAppointRegisterList: state => state.url + state.api.queryAppointRegisterList,
    queryNotPayList: state => state.url + state.api.queryNotPayList,
    queryPaiedList: state => state.url + state.api.queryPaiedList,
    queryReportInspe: state => state.url + state.api.queryReportInspe,//查询检验报告
    queryReportCheck: state => state.url + state.api.queryReportCheck,//查询检查报告
    queryReportDetail: state => state.url + state.api.queryReportDetail,//查询报告详情
    queryDayList: state => state.url + state.api.queryDayList,//查询每日清单
    queryPayRecord: state => state.url + state.api.queryPayRecord,//查询充值记录
    getJsApi: state => state.url + state.api.getJsApi,// 获取微信支付jsapi
    queryCardInfoById: state => state.url + state.api.queryCardInfoById,//通过ID查询his数据库病人信息
    ///////////////////////////////////住院服务模块///////////////////////////////////////////////
    queryInHospitalCertificate: state => state.url + state.api.queryInHospitalCertificate,//查询入院登记证信息
    inHospitalRegister: state => state.url + state.api.inHospitalRegister,//入院登记
    queryPatientInfoByID: state => state.url + state.api.queryPatientInfoByID,//根据住院号查询患者信息
    bindPatientInfo: state => state.url + state.api.bindPatientInfo,//绑定住院患者信息
    unbindPatientInfo: state => state.url + state.api.unbindPatientInfo,//解绑住院患者信息
    updatePatientInfo: state => state.url + state.api.updatePatientInfo,//更新住院患者信息
    queryPatientInfoList: state => state.url + state.api.queryPatientInfoList,//查询绑定的住院患者信息列表
    queryInHosList: state => state.url + state.api.queryInHosList,//查询住院清单
    queryInHosPayRecord: state => state.url + state.api.queryInHosPayRecord,//查询住院缴费记录
}

export default getters;


