<template>
    <div>
        <van-notice-bar mode="closeable" background="#0ab4a4" color="#fff" :wrapable="true">
            <template #default>
                <div class="notice-bar-text">您可预约明天至未来一周内所有坐诊的医生，预约完成后请于预约时间之前数小时到医院取号排队候诊。</div>
            </template>
        </van-notice-bar>
        <van-collapse v-model="activeNames" accordion>
            <van-collapse-item>
                <template #value>
                    <div class="collapse-item-val">
                        <van-icon :name="require('../../../assets/images/img/gh/obstetrics.png')" size="20"/>
                        <span>产科</span>
                    </div>
                </template>
                <template #default>
                        <van-cell class="collapse-item-val-sub" @click="handleGoDoctorList">
                            <template #title>
                                <div class="collapse-item-val-sub1">
                                    <van-icon :name="require('../../../assets/images/img/wapManager/dot.png')" size="20"/>
                                    <span>产科门诊</span>
                                </div>
                            </template>
                            <template #extra>
                                <div class="collapse-item-val-sub2">
                                    <van-icon size="12" :name="require('../../../assets/images/img/arrow_right.png')"/>
                                </div>
                            </template>
                        </van-cell>
                        <van-cell class="collapse-item-val-sub">
                            <template #title>
                                <div class="collapse-item-val-sub1">
                                    <van-icon :name="require('../../../assets/images/img/wapManager/dot.png')" size="20"/>
                                    <span>产科门诊</span>
                                </div>
                            </template>
                            <template #extra>
                                <div class="collapse-item-val-sub2">
                                    <van-icon size="12" :name="require('../../../assets/images/img/arrow_right.png')"/>
                                </div>
                            </template>
                        </van-cell>
                </template>
            </van-collapse-item>
            <van-collapse-item>
                <template #value>
                    <div class="collapse-item-val">
                        <van-icon :name="require('../../../assets/images/img/gh/gynaecology.png')" size="20"/>
                        <span>妇科</span>
                    </div>
                </template>
                <template #default>
                        <van-cell class="collapse-item-val-sub">
                            <template #title>
                                <div class="collapse-item-val-sub1">
                                    <van-icon :name="require('../../../assets/images/img/wapManager/dot.png')" size="20"/>
                                    <span>妇科部门</span>
                                </div>
                            </template>
                            <template #extra>
                                <div class="collapse-item-val-sub2">
                                    <van-icon size="12" :name="require('../../../assets/images/img/arrow_right.png')"/>
                                </div>
                            </template>
                        </van-cell>
                </template>
            </van-collapse-item>
            <van-collapse-item>
                <template #value>
                    <div class="collapse-item-val">
                        <van-icon :name="require('../../../assets/images/img/gh/pediatrics.png')" size="20"/>
                        <span>儿科</span>
                    </div>
                </template>
                <template #default>
                        <van-cell class="collapse-item-val-sub">
                            <template #title>
                                <div class="collapse-item-val-sub1">
                                    <van-icon :name="require('../../../assets/images/img/wapManager/dot.png')" size="20"/>
                                    <span>儿科部门</span>
                                </div>
                            </template>
                            <template #extra>
                                <div class="collapse-item-val-sub2">
                                    <van-icon size="12" :name="require('../../../assets/images/img/arrow_right.png')"/>
                                </div>
                            </template>
                        </van-cell>
                </template>
            </van-collapse-item>
        </van-collapse>
    </div>
</template>
<script>
export default {
    name: "CurrentRegister",
    data() {
        return {
            activeNames: []
        };
    },
    methods: {
        handleGoDoctorList() {
            this.$router.push({name: "DoctorList"});
        }
    }
};

</script>
<style scoped>
.notice-bar-text {
    font-size: 12px;
    text-align: left;
    line-height: 17px;
}
.van-collapse {
    margin-top: 20px;
}
.collapse-item-val {
    display: flex;
    align-items: center;
}
.collapse-item-val .van-icon {
    margin-right: 15px;
}
.collapse-item-val-sub {
    padding: 0;
    background-color: #ebebf1;
    padding: 12px 0;
}
.collapse-item-val-sub1{
    display: flex;
    align-items: center;
    padding-left: 40px;
    box-sizing: border-box;
    height: 100%;
}
.collapse-item-val-sub2 {
    padding-right: 4px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
}
.van-collapse-item>>>.van-collapse-item__content {
    padding: 0 16px;
    background-color: #ebebf1;
}
</style>



