<template>
    <div>
        <com-card @show-code="handleOnGoShowCode" :status="bindedInHosNo" :type="1" :data="dataSource"></com-card>
        <div class="main m-t-70 p-l-15 p-r-15">
            <div class="main-service p-b-15">
                <base-operate-bar :text="{firstTxt:'常用', secondTxt: '服务'}"></base-operate-bar>
                <van-grid :border="false" :column-num="4">
                    <van-grid-item>
                        <base-service-btn :text="'自助入院'" :icon="require('../../../assets/images/hospital/inhos.png')" :gradient="'#00CAFF,#008CFF'" @click.native="handleGoQueryInhosCerti()"></base-service-btn>
                    </van-grid-item>
                    <van-grid-item>
                        <base-service-btn :text="'续交押金'" :icon="require('../../../assets/images/pay.png')" :gradient="'#9DEB7E,#61C854'" @click.native="handleGoChargeDeposit('ChargeDeposit')"></base-service-btn>
                    </van-grid-item>
                    <van-grid-item>
                        <base-service-btn :text="'每日清单'" :icon="require('../../../assets/images/every_list.png')" :gradient="'#FFD418,#FDB404'" @click.native="handleGoDetailPage('InHosList')"></base-service-btn>
                    </van-grid-item>
                    <van-grid-item>
                        <base-service-btn :text="'住院管理'" :icon="require('../../../assets/images/hospital/manage_inhosInfo.png')" :gradient="'#6BDF6B,#3FD18B'" @click.native="handleGoDetailPage('ManageInhosInfo')"></base-service-btn>
                    </van-grid-item>
                </van-grid>
            </div>
            <div class="more-service">
                <base-operate-bar :text="{firstTxt:'更多', secondTxt: '服务'}"></base-operate-bar>
                <div class="more-service flex_dom flex_item_between m-t-10">
                    <div class="left">
                        <base-service-card :scale="'large'" :icon="require('../../../assets/images/hospital/more_service01.png')" :title="'报告查询'" :title-desc="'查看检查检验报告'" @click.native="handleGoQueryReport('QueryReport')"></base-service-card>
                    </div>
                    <div class="right flex_column flex_item_between">
                        <div class="hgt80">
                            <base-service-card :scale="'middle'" :icon="require('../../../assets/images/outpatient/more_service02.png')" :title="'为他人续交'" :title-desc="'为他人续交押金'" @click.native="handleGoDetailPage('QueryOthersInHosNo')"></base-service-card>
                        </div>
                        <div class="hgt80">
                            <base-service-card :scale="'middle'" :icon="require('../../../assets/images/hospital/more_service02.png')" :title="'结算信息查询'" :title-desc="'查看住院费用结算清单'"></base-service-card>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import CCard from "../../common/CCard";
import BOperateBar from "../../base/BOperateBar";
import BServiceBtn from "../../base/BServiceBtn";
import BServiceCard from "../../base/BServiceCard";
export default {
    name: "Outpatient",
    components: {
        [CCard.name]: CCard,
        [BOperateBar.name]: BOperateBar,
        [BServiceBtn.name]: BServiceBtn,
        [BServiceCard.name]: BServiceCard,
    },
    data() {
        return {
            border: false,
            dataSource: {},
        };
    },
    computed: {
        bindedInHosNo() {
            return this.$store.state.bindedInHosNo;
        }
    },
    mounted() {
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        let owner = this;
        owner.globalMethods.setRequestHeader();
        this.$axios({
                    method:'post',
                    url: owner.$store.getters.queryPatientInfoList,
                    data: {}
                }).then((response) => {
                    if (response.data.Code == 0) {
                        let data = response.data.Data;
                        for (let i = 0; i < data.length; i++) {
                            let item = data[i];
                            if (item.isDefault == 0) {
                                owner.dataSource = item;
                                break;
                            }
                        };
                        owner.$store.commit("bindedInHosNo", true);
                        owner.$toast.clear();
                    }
                    else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                    }
                    else {
                        owner.$store.commit("bindedInHosNo", false);
                        owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: false
                        });
                    }
                })
    },
    methods: {
        handleOnGoShowCode() {
            this.$router.push({name: "InHosCodeDetail"});
        },
        handleGoBindInHosNo() {
            this.$router.push({name: "BindInHosNo"});
        },
        handleGoQueryInhosCerti() {
            this.$router.push({name: "QueryInhosCertificate"});
        },
        handleGoQueryReport(name) {
            let query = {};
            query["type"] = 1;
            this.handleGoDetailPage(name, query);
        },
        handleGoChargeDeposit(name) {
            let query = {};
            query["inHosNo"] = this.dataSource.inHosNo;
            this.handleGoDetailPage(name, query);
        },
        handleGoDetailPage(name, query) {
            let routeName = this.bindedInHosNo ? name : "BindInHosNo";
            if (query) {
                this.$router.push({name: routeName, query});
            }
            else {
                this.$router.push({name: routeName});
            }
        }
    }
};

</script>

<style scoped>
.title {
    text-align: left;
}
.panel {
    background-color: #fff;
    color: #333;
    margin-bottom: 15px;
}
.custom-title-explain {
    color: #9e9e9e;
    font-size: 12px;
}

</style>



