<template>
    <div>
        <div class="card-detail-wrap p-l-15 p-r-15 p-t-25">
            <div class="profile flex_dom flex_item_mid">
                <div class="avatar pr m-r-15">
                    <img class="pa abs-mid-X" src="../../../assets/images/gentleman.png" v-if="true">
                    <img class="pa abs-mid-X" src="../../../assets/images/lady.png" v-if="false">
                    <span class="patSex pa bgimg100" :class="classObj"></span>
                </div>
                <div class="info flex_column flex_item_between color-fff">
                    <div class="name tl font18 font-w6">微信昵称</div>
                    <div class="code tl font12">河南 | 郑州</div>
                </div>
            </div>
            <div class="content m-t-25">
                <div class="panel p-l-15 p-r-15">
                    <div class="title tl">门诊服务</div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoMyRegister('MyRegister')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/appoint.png">
                            <span class="m-l-10">我的挂号</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoDetailPage('PayRecord')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/charge.png">
                            <span class="m-l-10">就诊卡充值记录</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoDetailPage('DayList')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/consume.png">
                            <span class="m-l-10">门诊消费记录</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell flex_dom flex_item_between flex_item_mid" @click="handleGoQueryOutPatientReport('QueryReport')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/report_outpat.png">
                            <span class="m-l-10">门诊报告查询</span>
                        </div>
                        <span class="value"></span>
                    </div>
                </div>
                <div class="panel p-l-15 p-r-15 m-t-10">
                    <div class="title tl">住院服务</div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoDetailPage('InHosPayRecord')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/deposit.png">
                            <span class="m-l-10">住院押金预交记录</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoDetailPage('InHosList')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/list_inhos.png">
                            <span class="m-l-10">住院日清单</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell flex_dom flex_item_between flex_item_mid" @click="handleGoQueryInHosReport('QueryReport')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/report_inhos.png">
                            <span class="m-l-10">住院报告查询</span>
                        </div>
                        <span class="value"></span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "account",
    data() {
        return {
            dataSource: "",
            wxData: {
                patSex: "1"
            }
        };
    },
    computed: {
        classObj() {
            return {
                gentleman: this.wxData.patSex == 1,
                lady: this.wxData.patSex == 2
            }
        },
        binded() {
            return this.$store.state.binded;
        },
    },
    mounted() {
        this.queryCardInfo();
    },
    methods: {
        handleGoMyRegister(name) {
            let query = {};
            query["patId"] = this.dataSource.patId;
            query["patMedNo"] = this.dataSource.patMedNo;
            this.handleGoDetailPage(name, query);
        },
        handleGoQueryOutPatientReport(name) {
            let query = {};
            query["type"] = 0;
            this.handleGoDetailPage(name, query);
        },
        handleGoQueryInHosReport(name) {
            let query = {};
            query["type"] = 1;
            this.handleGoDetailPage(name, query);
        },
        handleGoDetailPage(name, query) {
            let routeName = this.binded ? name : "BindCard";
            if (query) {
                this.$router.push({name: routeName, query});
            }
            else {
                this.$router.push({name: routeName});
            }
        },
        queryCardInfo() {
            let owner = this;
            // 把token设置到请求头里面
            owner.globalMethods.setRequestHeader();
            owner.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
            owner.$axios({
                    method: 'post',
                    url: owner.$store.getters.queryCardInfo,
                    data: {patId: ""}
                }).then ((response) => {
                    if (response.data.Code == 0) {
                        let dataArry = response.data.Data;
                        for (let i = 0; i < dataArry.length; i++) {
                            let item = dataArry[i];
                            if (item.isDefault == 0) {
                                owner.dataSource = item;
                                break;
                            }
                        }
                        owner.$store.commit("binded", true);
                        owner.$toast.clear();
                    }
                    else if (response.data.Code == -2) {
                        owner.$toast({
                            type: "fail",
                            message: "toke过期，即将重新登录",
                            forbidClick: true,
                            onOpened: () => {
                                owner.globalMethods.delCookie("token");
                                owner.$router.push("Outpatient");
                            }
                        });
                    }
                    else {
                        owner.$store.commit("binded", false);
                        owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: true
                        })
                    };
                })
        },
    }
};
</script>
<style scoped>
.card-detail-wrap {
    height: 100vh;
    background: url('../../../assets/images/card_bg.png') no-repeat;
    background-size: 100%;
    background-color: #F3F5F9;
}
.profile .avatar {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    border: 1px solid #7BB6EC;
    background-color: #E3F4FC;
}
.profile .avatar img {
    width: 70%;
    height: 80%;
    bottom: 0;
}
.profile .avatar .patSex {
    width: 17px;
    height: 16px;
    bottom: 0;
    right: -2px;
}
.profile .avatar .patSex.gentleman {
    background: url('../../../assets/images/man.png') no-repeat;
}
.profile .avatar .patSex.lady {
    background: url('../../../assets/images/woman.png') no-repeat;
}
.profile .info {
    height: 45px;
}
.content .panel {
    background-color: #fff;
    border-radius: 10px;
}
.content .panel .title {
    height: 48px;
    line-height: 48px;
    color: #292C34;
    font-size: 18px;
    font-weight: bold;
}
.content .panel .cell {
    height: 46px;
    line-height: 46px;
}
.content .panel .cell .icon {
    width: 22px;
    height: 22px;
}
.border-bottom {
    border-bottom: 0.5px solid #EEEEEE;
}
.content .panel .value {
    width: 25px;
    height: 25px;
    background: url('../../../assets/images/account/right.png') no-repeat;
    background-size: 100% 100%;
}
.balance {
    font-size: 18px;
    color: #FF6257;
}
.charge {
    font-size: 13px;
    color: #0078FF;
    padding: 10px;
}

</style>


