<template>
    <div class="container">
        <div class="base-info">
            <van-cell title="姓名" title-class="title-style" :icon="require('../../../assets/images/img/bs_name.png')" :value="dataSource.patName" value-class="value-style"/>
            <van-cell title="就诊卡号" title-class="title-style" :icon="require('../../../assets/images/img/bs_hospitalNum.png')" :value="dataSource.patMedNo" value-class="value-style"/>
            <van-cell title="科室" title-class="title-style" :icon="require('../../../assets/images/img/dd-keshi.png')" :value="dataSource.deptName" value-class="value-style"/>
            <van-cell title="医生姓名" title-class="title-style" :icon="require('../../../assets/images/img/dd-yisheng.png')" :value="dataSource.doctorName" value-class="value-style"/>
            <van-cell title="挂号费用" title-class="title-style" :icon="require('../../../assets/images/img/dd-feiyong.png')" :value="dataSource.totalFee" value-class="value-style danger"/>
            <van-cell title="就诊时间" title-class="title-style" :icon="require('../../../assets/images/img/dd-riqi.png')" value-class="value-style">
                <template #default>
                    <div>{{dataSource.appointDate}}</div>
                </template>
            </van-cell>
        </div>
        <div class="clinic-type">
            <div class="title">就诊类型</div>
            <van-radio-group v-model="clinicType">
                <van-radio name="0">初诊</van-radio>
                <van-radio name="1">复诊</van-radio>
            </van-radio-group>
        </div>
        <van-button class="submit" round block type="info" :loading="submitLoading" loading-text="请稍后" :disabled="submitDisabled" @click="handleSubmit">提交订单</van-button>
        <div class="notes">注：请于预约日就诊前到"我的挂号"页支付或到院支付，如无法就诊，请提前取消预约。</div>
        <van-dialog v-model="show" confirm-button-text="我知道了" @confirm="handleGoMyRegister">
            <div class="dialog-content">
                <div class="icon">
                    <van-icon size="30" :name="require('../../../assets/images/img/dd-chenggong.png')" />
                    <span>预约成功！</span>
                </div>
                <div class="code">{{passWord}}</div>
                <div class="tips">支付完成后带预约码到分诊台取号排队候诊</div>
            </div>
        </van-dialog>
    </div>
</template>
<script>
export default {
    name: "RegisterDetail",
    data() {
        return {
            clinicType: "0",
            show: false,
            dataSource: "",
            passWord: "",
            submitLoading: false,
            submitDisabled: false
        };
    },
    mounted() {
        this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
        let owner = this;
        owner.globalMethods.setRequestHeader();
        this.$axios({
                        method:'post',
                        url: owner.$store.getters.queryCardInfo,
                        data: {patId: ""}
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            let dataArr = response.data.Data;
                            for (let i = 0; i < dataArr.length; i++) {
                                let curItem = dataArr[i];
                                if (curItem.isDefault == 0) {
                                    let dataSource = {};
                                    let routeData = owner.$route.params.data;
                                    let userInfo = {};
                                    userInfo["patId"] = curItem.patId;
                                    userInfo["patName"] = curItem.patName;
                                    userInfo["patMedNo"] = curItem.patMedNo;
                                    Object.assign(dataSource, routeData, userInfo);
                                    owner.dataSource = dataSource;

                                    break;
                                }
                            }
                            owner.$toast.clear();
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.$toast({
                                    type: "fail",
                                    message: response.data.Msg,
                                    forbidClick: true
                                });
                        }
                    })
    },
    methods: {
        handleSubmit() {
            this.submitDisabled = true;
            this.submitLoading = true;
            let data = this.dataSource;
            data["clinicType"] = this.clinicType;
            data["type"] = 0;
            data["appointTime"] = "";
            data["passWord"] = "";
            let owner = this;
            owner.globalMethods.setRequestHeader();
            this.$axios({
                        method:'post',
                        url: owner.$store.getters.appointRegisterOrCancel,
                        data
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            owner.passWord = response.data.Data.passWord;
                            owner.show = true;
                        }
                        else if (response.data.Code == -2) {
                            owner.$toast({
                                type: "fail",
                                message: "toke过期，即将重新登录",
                                forbidClick: true,
                                onOpened: () => {
                                    owner.globalMethods.delCookie("token");
                                    owner.$router.push("Outpatient");
                                }
                            });
                        }
                        else {
                            owner.$toast({
                                    type: "fail",
                                    message: response.data.Msg,
                                    forbidClick: true,
                                    onOpened: () => {
                                        owner.submitDisabled = false;
                                        owner.submitLoading = false;
                                    }
                                });
                        }
                    })
        },
        handleGoMyRegister() {
            let patId = this.dataSource.patId;
            let patMedNo = this.dataSource.patMedNo;
            this.$router.replace({name: "MyRegister", query: {patId, patMedNo}});
        }
    }
};

</script>
<style scoped>
.container {
    min-height: 100vh;
    background-color: #F3F5F9;
    padding: 10px 15px;
}
.title-style {
    text-align: left;
    color: #877e7e;
}
.value-style {
    color: black;
    overflow: visible;
}
.danger {
    color: #ff6257;
}
.clinic-type {
    padding: 10px 20px;
    background-color: #fff;
    margin-top: 10px;
    border-radius: 10px;
    overflow: hidden;
}
.clinic-type .title {
    text-align: left;
    font-weight: bold;
}
.clinic-type .van-radio-group {
    padding: 15px;
}
.clinic-type .van-radio:nth-child(1) {
    margin-bottom: 10px;
}
.clinic-type .van-radio:last-child {
    margin-bottom: 0px;
}
.submit {
    width: 80%;
    margin: 20px auto;
}
.notes {
    color: #9c9c9c;
    font-size: 14px;
    text-align: left;
    padding: 10px;
}
.dialog-content {
    padding: 20px 20px 40px 20px;
}
.dialog-content .icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.dialog-content .icon>span {
    font-size: 14px;
    color: #0078ff;
    text-align: center;
}
.dialog-content .code {
    margin-top: 20px;
    color: #ff6257;
    background-color: #e5e7e9;
    padding: 5px;
}
.dialog-content .tips {
    font-size: 14px;
    color: #999;
    margin-top: 15px;
}
.base-info {
    border-radius: 10px;
    overflow: hidden;
}
</style>



