<template>
    <div>
        <com-code-detail :type="0" :data="dataSource"></com-code-detail>
    </div>
</template>
<script>
import CCodeDetail from "../../common/CCodeDetail";
export default {
    name: "CodeDetail",
    components: {
        [CCodeDetail.name]: CCodeDetail,
    },
    data() {
        return {
            dataSource: []
        };
    },
    mounted() {
        let patId = this.$route.query.patId;
        this.queryCardInfo(patId);
    },
    methods: {
        queryCardInfo(patId) {
            let owner = this;
            owner.globalMethods.setRequestHeader();
            owner.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
            owner.$axios({
                    method: 'post',
                    url: owner.$store.getters.queryCardInfo,
                    data: {patId: ""}
                }).then ((response) => {
                    if (response.data.Code == 0) {
                        let resData = response.data.Data;
                        if (patId) {
                            for (let i = 0; i < resData.length; i++) {
                                let item = resData[i];
                                item.isDefault = (item.patId == patId) ? "0" : "1";
                            }
                        }
                        owner.dataSource = resData;
                        owner.$toast.clear();
                    }
                    else if (response.data.Code == -2) {
                        owner.$toast({
                            type: "fail",
                            message: "toke过期，即将重新登录",
                            forbidClick: true,
                            onOpened: () => {
                                owner.globalMethods.delCookie("token");
                                owner.$router.push("Outpatient");
                            }
                        });
                    }
                    else {
                        owner.$toast({
                            type: "fail",
                            message: response.data.Msg,
                            forbidClick: true
                        })
                    };
                })
        },
    }
};
</script>
<style scoped>

</style>


